import Network from '@yl-app-jssdk/network';
import localstorageApi from '@yl/localstorage';
import context from '@yl-app-jssdk/context';
import AppAuthService from '@services/app-auth-service';
import UserService from '@services/user-service';
import {
  internalAppPlugins,
  externalAppPlugins,
} from './webview-inject-plugins';

let webviewOpening = false; // 记录webview是否正在打开

const openMCordovaWebView = (url, plugins) => {
  window.MCordovaWebView.Show(
    url,
    () => {
      webviewOpening = false;
      console.log('打开成功');
    },
    err => {
      webviewOpening = false;
      console.error(err);
    },
    {
      injectPlugins: plugins, // pluginIds,
      adjustSafeArea: true, // 配浏海屏安全区域；
    }
  );
};

/**
 * 打开内部微应用前校验token是否过期
 * @param {*} url
 */
const checkInternalApp = url => {
  const refreshToken = localstorageApi.getString('super_refresh_token');
  if (refreshToken) {
    url += '&access_token=' + context.getToken();
    webviewOpening = true;
    openMCordovaWebView(url, internalAppPlugins);
  } else {
    UserService.refreshToken().then(res => {
      if (res) {
        // 设置10分钟过期，10分钟内不刷新token
        localstorageApi.set('super_refresh_token', res, 0, 60 * 10);
        url += '&access_token=' + res;
        webviewOpening = true;
        openMCordovaWebView(url, internalAppPlugins);
      }
    });
  }
};

/**
 * 打开外部微应用前获取临时授权凭证
 * @param {*} url
 */
const checkExternalApp = url => {
  url += '&access_token=' + context.getToken();
  webviewOpening = true;
  AppAuthService.appAuth()
    .then(code => {
      url += '&code=' + code;
      openMCordovaWebView(url, externalAppPlugins);
    })
    .catch(() => {
      openMCordovaWebView(url, externalAppPlugins);
    });
};

export default {
  // appType: 微应用类型
  openUrlWebView(url, appType) {
    if (webviewOpening) return;
    Network.getHasNetwork().then(hasNetwork => {
      if (hasNetwork) {
        const project =
          localstorageApi.getObject('cjap_zjapp_home_page_default_project') ||
          {};
        url +=
          '?project_id=' +
          project.id +
          '&project_name=' +
          encodeURI(project.name);
        if (appType === 'external') {
          checkExternalApp(url);
        } else {
          checkInternalApp(url);
        }
      } else {
        this.$toast('请先联网在进行此操作');
      }
    });
  },
};
