import Plugin from '@common/plugin';
import Vue from 'vue';
import { getIsLogged } from '@yl-app-jssdk/context';
import CONST from '@config/const';
import AUTH from '@config/auth';
import ConfigService from './config-service';
import { getUserPermissions } from './permission-service';
import _ from 'underscore';

const creatCode = function(menuCode = '', functionCode = '') {
  let code = [];
  menuCode && code.push(menuCode);
  functionCode && code.push(functionCode);
  return code.join('_');
};

/**
 * 格式化config
 * @param {*} productCode
 * @param {*} permissions
 * @param {*} config
 */
const formatConfig = (productCode, permissions, config) => {
  // 先过滤头部的
  config.topModules.forEach(v => {
    // 外部微应用,则直接显示
    if (!v.product && v.type) {
      v.isShow = true;
    }
    if (v.product === productCode) {
      let code = creatCode(v.menuCode, v.functionCode);

      v.isShow = true;

      // 没权限，删除
      if (!v.noCheck && !~permissions.indexOf(code)) {
        v.isShow = false;
      }
    }
  });

  config.modules.forEach((v, k) => {
    if (!v.product || v.product === productCode) {
      v.functions.forEach(f => {
        // 外部微应用,则直接显示
        if (!f.product && f.type) {
          f.isShow = true;
        }
        // 没有产品属性或不是当前产品的模块
        if (!v.product && f.product != productCode) {
          return;
        }

        f.isShow = true;
        let code = creatCode(f.menuCode, f.functionCode);
        // 没权限，删除
        if (!f.noCheck && !~permissions.indexOf(code)) {
          f.isShow = false;
        }
      });

      let index = _.findWhere(v.functions, {
        isShow: true,
      });

      if (!index) {
        if (v.default) {
          v.default.isShow = true;
          config.modules[k].functions.push(v.default);
        } else {
          config.modules[k].isShow = false;
        }
      }
    }
  });

  return config;
};

export default {
  scanQR() {
    return Plugin.scanQRCode('请将二维码放入框内', true, () => {
      Vue.prototype.$toast('抱歉，无法扫描');
    })
      .then(result => {
        window[`__module_entry_gcxt__`].superAppQrScan(result);
      })
      .catch(err => {
        console.error(err);
        Vue.prototype.$toast(err || '扫描失败', 3000);
      });
  },

  getHomePageConfig: () => {
    if (!getIsLogged()) {
      return Promise.reject('用户未登录');
    }
    return ConfigService.getPageConfig(CONST.WORKBENCH_PAGE).then(config => {
      // 获取各个产品的权限
      let promiseArr = [];
      Object.keys(AUTH).forEach(v => {
        promiseArr.push(
          (function(code, product) {
            if (Array.isArray(code)) {
              const func = [];
              code.forEach(item => {
                func.push(getUserPermissions(item));
              });

              return Promise.all(func)
                .then(res => {
                  if (!_.isEmpty(res[0]) || !_.isEmpty(res[1])) {
                    config = formatConfig(
                      product,
                      res[0].concat(res[1]),
                      config
                    );
                  }
                })
                .catch(err => {
                  console.error(code + ' : getUserPermissions error', err);
                });
            } else {
              return getUserPermissions(code)
                .then(res => {
                  // 有权限，过滤可以展示的产品，没有权限表示该产品的所有功能都不展示
                  if (!_.isEmpty(res)) {
                    config = formatConfig(product, res, config);
                  }
                  // 除了工程协同外，没有权限的产品删除
                  // if (_.isEmpty(res) && v.code != 'gcxt') {
                  //   products[index].enabled = false;
                  // }
                })
                .catch(err => {
                  console.error(code + ' : getUserPermissions error', err);
                });
            }
          })(AUTH[v], v)
        );
      });

      // 获取用户权限
      return Promise.all(promiseArr)
        .then(() => {
          let result = {};

          result.topModules = config.topModules.filter(v => {
            return v.isShow;
          });

          result.modules = config.modules.filter(v => {
            return !!_.findWhere(v.functions, {
              isShow: true,
            });
          });

          result.modules.forEach(v => {
            let functions = v.functions.filter(k => {
              return k.isShow;
            });
            v.functions = functions;
          });

          return Promise.resolve(result);
        })
        .catch(err => {
          console.error('getUserPermissions error', err);
          return Promise.resolve({});
        });
    });
  },
};
