export const APP = 'APP';

export default {
  namespaced: true,
  state: {
    global: {
      isIOS: false,
      isIOSX: false,
      headerHeight: 44, // 顶部banner的高度（包含ios顶部空白）
      footerHeight: 50,
      clientHeight: 568,
      clientWidth: 320,
      contentHeight: 524,
    },
    isAppStoreAccount: false,
    isInit: false,
    lastUpdateTime: {
      workbench: 0,
    },
    hasProjectSelect: false,
    loadingStatus: {},
    isOverwriteInstallation: false,
  },
  mutations: {
    [APP]: (state, payload) => {
      state.global = Object.assign({}, state.global, payload);
    },
    PROJECT_SELECT: state => {
      state.hasProjectSelect = true;
    },
    LAST_UPDATE_TIME: (state, payload) => {
      state.lastUpdateTime = Object.assign({}, state.lastUpdateTime, payload);
    },
    LOADING_STATUS: (state, payload) => {
      state.loadingStatus = Object.assign({}, state.loadingStatus, payload);
    },

    INIT_STATUS: (state, payload) => {
      state.isInit = payload;
    },

    APP_STORE_ACCOUNT: (state, payload) => {
      state.isAppStoreAccount = payload;
    },

    CLEAR_LOADING_STATUS: state => {
      for (let i in state.loadingStatus) {
        state.loadingStatus[i] = false;
      }
    },

    SET_OVERWRITE_INSTALLTION: (state, payload) => {
      state.isOverwriteInstallation = payload;
    },

    REST: state => {
      state.lastUpdateTime = {};
    },
  },

  actions: {
    reset({ commit }) {
      commit('REST');
    },
  },
};
