import device from '@yl-app-jssdk/device';
/**
 * 获取当前时间 yyyy-MM-dd hh:mm:ss (部分iOS版本通过Date获取日期会出现格式的问题)
 *
 * @returns {string} yyyy-MM-dd hh:mm:ss
 */
const getNowDate = () => {
  let now = new Date();
  let year = now.getFullYear();
  let month = now.getMonth() + 1;
  let day = now.getDate();
  let hours = now.getHours();
  let minute = now.getMinutes();
  let second = now.getSeconds();

  if (month < 10) {
    month = '0' + month;
  }

  if (day < 10) {
    day = '0' + day;
  }

  if (hours < 10) {
    hours = '0' + hours;
  }

  if (minute < 10) {
    minute = '0' + minute;
  }

  if (second < 10) {
    second = '0' + second;
  }

  return `${year}-${month}-${day} ${hours}:${minute}:${second}`;
};
function defered() {
  var dfd = {};
  dfd.promise = new Promise(function(resolve, reject) {
    dfd.resolve = resolve;
    dfd.reject = reject;
  });
  return dfd;
}
/**
 * 打开图片浏览器
 * @param imgUrls {array} 图片路径数组（全路径）
 * @param selectedIndex {int} 选中图片索引
 * @returns {promise}
 */
const openImageBrowser = (imgUrls, selectedIndex) => {
  selectedIndex = selectedIndex || 0;
  console.log('openImageBrowser: ', imgUrls, selectedIndex);

  return device.ready().then(() => {
    let defer = defered();

    window.MImageBrowser.openWithCallback(imgUrls, selectedIndex, {
      onOpen: function() {
        console.log('openImageBrowser: onOpen');
      },
      onError: function(error) {
        console.log('openImageBrowser: onError (open error)');
        console.error('openImageBrowser:', error);

        defer.reject(error);
      },
      onDownloadImage: function(saveInfo) {
        console.log('openImageBrowser: onDownloadImage', saveInfo);

        if (!saveInfo || !saveInfo.url || !saveInfo.path) {
          console.error('openImageBrowser: invalid saved info -> ', saveInfo);
        } else {
          defer.notify(saveInfo);
        }
      },
      onClose: function() {
        console.log('openImageBrowser: onClose');

        // 恢复状态样款风格
        // window.StatusBar && window.StatusBar.styleDefault();

        defer.resolve();
      },
    });

    return defer.promise;
  });
};

const getUrlParams = url => {
  let reg = new RegExp('[^?]+$');

  let params = reg.exec(url);

  let result = {};
  params = params[0].split('&');
  params.forEach(function(v) {
    let item = v.split('=');
    result[item[0]] = item[1];
  });

  return result;
};
export default {
  getNowDate,
  defered,
  openImageBrowser,
  getUrlParams,
};
