import context from '@yl-app-jssdk/context';
import Vue from 'vue';
import VueRouter from 'vue-router';

import Login from '@src/views/login.vue';
import Debugger from '@src/views/debugger';
import Layout from '@src/views/layout.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/debugger',
    name: 'debugger',
    meta: {
      ignore: true,
    },
    component: Debugger,
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      ignore: true,
    },
    component: Login,
  },
  {
    path: '/bind-phone',
    name: 'bindPhone',
    meta: {
      ignore: true,
    },
    component: () => import('@src/views/bindphone.vue'),
  },
  {
    // 选择租户
    path: '/tenant-list',
    name: 'tenantList',
    meta: {
      ignore: true,
    },
    component: () => import('@src/views/tenant-list.vue'),
  },
  {
    // 选择待审核租户
    path: '/verify-tenant-list',
    name: 'verifyTenantList',
    meta: {
      ignore: true,
    },
    component: () => import('@src/views/verify-tenant-list.vue'),
  },
  {
    path: '/',
    name: 'layout',
    component: Layout,
    redirect: 'workbench',
    children: [
      {
        path: 'workbench',
        name: 'workbench',
        component: () => import('@src/views/workbench/index.vue'),
        meta: {
          isNav: true,
        },
      },
      {
        path: 'todo',
        name: 'todo',
        component: () => import('@src/views/todo/index.vue'),
        meta: {
          isNav: true,
        },
      },
      {
        path: 'user',
        name: 'user',
        component: () => import('@src/views/user/index.vue'),
        meta: {
          isNav: true,
        },
      },
    ],
  },
  {
    // 消息
    path: '/message/index',
    name: 'messageIndex',
    component: () => import('@src/views/message/index.vue'),
  },
  {
    // 消息详情
    path: '/message/detail/:id',
    name: 'messageDetail',
    component: () => import('@src/views/message/detail.vue'),
  },
  {
    // 权限申请
    path: '/user/permission-apply',
    name: 'permissionApply',
    component: () => import('@src/views/user/permission/permission-apply.vue'),
  },
  {
    // 审核详情
    path: '/user/apply-info',
    name: 'applyInfo',
    component: () => import('@src/views/user/permission/apply-info.vue'),
  },
  {
    // 项目管理
    path: '/user/project-manage',
    name: 'projectManage',
    component: () => import('@src/views/user/permission/project-manage.vue'),
  },
  {
    // 帮助与反馈
    path: '/user/feedback',
    name: 'feedback',
    component: () => import('@src/views/user/feedback/index.vue'),
  },
  {
    // 用户信息
    // 帮助与反馈 历史
    path: '/user/feedback/history',
    name: 'feedbackHistory',
    component: () => import('@src/views/user/feedback/history.vue'),
  },
  {
    path: '/user/info',
    name: 'userInfo',
    component: () => import('@src/views/user/info.vue'),
  },
  {
    // 修改密码
    path: '/user/password',
    name: 'password',
    component: () => import('@src/views/user/password.vue'),
  },
  {
    // 设置
    path: '/user/setting',
    name: 'setting',
    component: () => import('@src/views/user/setting.vue'),
  },
  {
    // 切换建设单位
    path: '/user/tenant-list',
    name: 'userTenantList',
    component: () => import('@src/views/user/tenant-list'),
  },
  {
    // 角色信息二级详情
    path: '/user/info-detail',
    name: 'infoDetail',
    component: () => import('@src/views/user/info-detail'),
  },
  {
    // 合同管理
    path: '/user/contract-manage',
    name: 'contractManage',
    component: () => import('@src/views/user/permission/contract-manage'),
  },
  {
    // 项目或者合同审核详情
    path: '/user/apply-detail',
    name: 'applyDetail',
    component: () => import('@src/views/user/permission/apply-detail'),
  },
  {
    // 关于
    path: '/user/about',
    name: 'about',
    component: () => import('@src/views/user/about.vue'),
  },
  {
    // 公告列表
    path: '/announcement/list',
    name: 'announcementIndex',
    component: () => import('@src/views/announcement/index.vue'),
  },
  {
    // 公告详情
    path: '/announcement/detail/:id',
    name: 'announcementDetail',
    component: () => import('@src/views/announcement/detail.vue'),
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});
// if (process.env.VUE_APP_ENV !== 'dev') {
router.beforeEach((routeTo, routeFrom, next) => {
  console.log('routeFrom.name =====> ', routeFrom.name);
  console.log('routeTo.name =====> ', routeTo.name);
  // 在登录页面或者是已登录
  if (
    routeTo.meta.ignore ||
    window.location.hash.startsWith('#/login') ||
    context.getIsLogged()
  )
    return next();

  // 未登录跳回登录页面
  redirectToLogin();

  function redirectToLogin() {
    console.log('redirectToLogin routeTo.name =====> ', routeTo.name);
    next({
      path: '/login',
      query: {
        redirectFrom: routeTo.fullPath,
      },
    });
  }
});
// }

export default router;
