import http from '@yl-app-jssdk/http';
import qs from 'qs';
import context from '@yl-app-jssdk/context';
// import router from '@router';
import device from '@yl-app-jssdk/device';
import version from '@src/common/version';
import UserService from '@services/user-service';
import LoginService from '@services/login-service';

const TOKEN_KEYS = ['access_token', 'unified_token'];

function formatConfig(url, options, data, appInfo, method = 'get') {
  let user = context.getUserContext();
  let token = user.token;
  let tenantCode = user.tenantCode || window.TENANT_CONFIG.TENANT_CODE;

  // 处理url
  url +=
    (~url.indexOf('?') ? '&' : '?') +
    `o=${tenantCode}&app_version=${appInfo.appVersion}&platform=${appInfo.appPlatform}&deviceId=${appInfo.deviceId}`;

  if (options.token !== false) {
    url += `&accessToken=${token}`;
  }

  let config = {
    url,
    method,
    ...options,
  };

  TOKEN_KEYS.forEach(v => {
    data[v] && (data[v] = token);
  });

  if (method.toLocaleLowerCase() === 'get') {
    config.params = data;
  } else {
    config.data = qs.stringify(data);
  }
  console.log(config, 'ajax config');

  return config;
}

function isOvertime(res) {
  if (res.errcode === 10021 || res.errcode === 3002 || res.success === '0000') {
    return true;
  }

  const msg = res.message || res.msg || '';

  if (!msg) {
    return false;
  }

  if (msg === '获取登录用户信息失败') {
    return true;
  }

  if (~msg.indexOf('过期') || ~msg.indexOf('失效')) {
    return true;
  }

  return false;
}

function wrapPromiseWithAutoReLogin(requestFunc, retry = true) {
  function loginErrorAutoReTry() {
    if (retry) {
      // 若尝试登录次数未达上限, 则重试登录
      return UserService.refreshToken()
        .then(function() {
          console.log('wrapPromiseWithAutoReLogin: reAutoLogin OK');
          // 登录成功后,仍然使用剩余重试次数,处理自动重新登录.
          //      处理promiseFunction中多个耗时且需登录的任务
          return wrapPromiseWithAutoReLogin(requestFunc, false);
        })
        .catch(err => {
          console.error(err);
          LoginService.logout();
          return Promise.reject(err);
        });
    } else {
      LoginService.logout();
      return Promise.reject('登录超时，请重试');
    }
  }

  // 1. 执行原Promise
  // 2. 捕获原Promise异常
  return requestFunc().then(({ data: result }) => {
    if (retry) {
      if (isOvertime(result)) {
        // if (retry) {
        // 处理登录超时的情况
        return loginErrorAutoReTry();
        // } else {
        //   LoginService.logout();
        //   return Promise.reject('登录超时，请重试');
        // }
      }
    } else if (result.errcode > 0) {
      // 检测到有其他设备登录
      if (result.errcode === 10023) {
        LoginService.logout(
          '检测到您的账号在其他设备登录，如果这不是您的操作请重新登录，并联系管理员！'
        );
      }
      return Promise.reject(result.message);
    }
    return Promise.resolve(result);
  });
}

export default function({ url, method = 'get', data = {}, ...options }) {
  return Promise.all([
    version.getAppFullVersion(),
    Promise.resolve(device.getDeviceInfo()),
  ])
    .then(function(results) {
      console.log('results ====> ', results);
      return {
        appVersion: results[0].fullVersion,
        appPlatform: results[1].platform,
        deviceId: results[1].uuid,
      };
    })
    .then(res => {
      if (options.noReLogin) {
        let config = formatConfig(url, options, data, res, method);
        return http.ajax(config).then(({ data: result }) => {
          if (result.errcode > 0) {
            return Promise.reject(result);
          }
          return Promise.resolve(result);
        });
      }

      return wrapPromiseWithAutoReLogin(function() {
        // 刷新token
        let config = formatConfig(url, options, data, res, method);

        return http.ajax(config);
      }, options.retry);
    });
}
