export default {
  namespaced: true,
  state: {
    workbench: {
      topModules: [],
      modules: [],
    },
    todo: {
      list: [],
    },
  },
  mutations: {
    SET_WORKBENCH_MODEL(state, payload) {
      state.workbench.topModules = payload.topModules || [];
      state.workbench.modules = payload.modules || [];
    },
    SET_TODO_LIST(state, payload) {
      state.todo.list = payload || [];
    },
  },
};
