import Vue from 'vue';
import promise from '@src/utils/defer';
// import pluginApi from './pluginApi';
import progressCmp from '@components/progress-bar';
import popupProgressCmp from '@components/popup-progress';
import hotUpdateAlertCmp from '@components/hot-update-alert';

if (!Vue.$comComps) {
  Vue.use(progressCmp)
    .use(hotUpdateAlertCmp)
    .use(popupProgressCmp);
}

const openUrlInBrowser = url => {
  if (window.MUtils && window.MUtils.openUrlInBrowser) {
    window.MUtils.openUrlInBrowser(url);
  }
};
/**
 * 版本更新提示
 *
 * @param {string} [title='']
 * @param {*} [details=[]]
 * @param {string} [downloadUrl='']
 * @param {boolean} [showCancel=false]
 * @param {string} [cancelText='下次更新']
 * @param {*} [cancelFunc=null]
 * @returns
 */
const showVersionAlert = (
  title = '',
  details = [],
  downloadUrl = '',
  showCancel = false,
  cancelText = '下次更新',
  cancelFunc = null
) => {
  let defer = promise.defer();
  Vue.$comComps.hotUpdateAlert.show({
    title: title,
    content: `<p>${details.join('</p><p>')}</p>`,
    okText: '立即更新',
    cancelText: cancelText,
    showCancel: showCancel,
    ok: () => {
      downloadUrl && openUrlInBrowser(downloadUrl);
      showCancel && Vue.$comComps.hotUpdateAlert.hide();
      defer.reject('正在更新大版本');
    },
    cancel: () => {
      typeof cancelFunc === 'function' && cancelFunc();
      defer.resolve();
    },
  });

  return defer.promise;
};

/**
 * 进度条
 */
const progress = {
  start: (title = '正在更新版本') => {
    Vue.$comComps.progressBar.show(title);

    Vue.$comComps.progressBar.progress(0);
  },

  update: (progress = 5) => {
    Vue.$comComps.progressBar.progress(progress);
  },

  finish: () => {
    Vue.$comComps.progressBar.progress(100);
    Vue.$comComps.progressBar.hide();
  },

  hide: Vue.$comComps.progressBar.hide,
};

/**
 * popup进度条
 */
const popupProgress = {
  currProgress: 0,
  timer: null,
  start: function(title = '正在下载') {
    this.currProgress = 0;
    this.timer = null;
    Vue.$comComps.popupProgress.show(title);
    Vue.$comComps.popupProgress.progress(0);
  },

  update: function(progress = 5) {
    console.log('progress', progress);
    console.log('currProgress', this.currProgress);
    if (this.timer) {
      clearInterval(this.timer);
      Vue.$comComps.popupProgress.progress(this.currProgress);
    }
    let i = 1;
    this.timer = setInterval(() => {
      if (i <= progress) {
        this.currProgress++;
        Vue.$comComps.popupProgress.progress(this.currProgress);
        i++;
      }
    }, 50);
  },

  finish: function() {
    Vue.$comComps.popupProgress.progress(100);
    Vue.$comComps.popupProgress.hide();
    clearInterval(this.timer);
  },

  hide: Vue.$comComps.popupProgress.hide,
};

export { showVersionAlert, progress, popupProgress };
