import version from '@src/common/version';
import { submitFeedback } from '@src/api/feedback';
import { getUserContext } from '@yl-app-jssdk/context';
import FileUploadService from './file-upload-service';
import network from '@yl-app-jssdk/network';
import device from '@yl-app-jssdk/device';
import Vue from 'vue';
import promise from '@src/utils/defer';
import DataCenterService from '@services/data-center-service';

let _self = {};
let modules = require('@config/modules.js');

function getFiles() {
  let files = [];
  let promises = [];
  Object.keys(modules).forEach(v => {
    promises.push(DataCenterService.getFeedbackFiles(v));
  });

  return Promise.all(promises).then(results => {
    results.forEach(v => {
      files = files.concat(v);
    });

    return files;
  });
}

/**
 * 提交反馈
 * @param feedback {
 *  {
 *      images:array,
 *      remark:string
 *  }
 * }
 * @returns {promise}
 */
_self.submitFeedback = function(feedback) {
  let user = getUserContext();

  let uploadImageTask;

  if (feedback.images && feedback.images.length) {
    uploadImageTask = FileUploadService.uploadFiles(feedback.images);
  }

  return Promise.all([
    // 1. 上传图片
    uploadImageTask,
    // 2. 获取设备信息
    Promise.resolve(device.getDeviceInfo()),
    // 3. 获取版本信息
    version.getAppFullVersion(),
  ]).then(function(result) {
    // 4. 组织数据格式
    feedback = Object.assign({}, feedback, {
      tenant_code: user.tenantCode,
      user_account: user.account,
      user_name:
        user.tenantInfo.tenantName || user.realname || user.userName || '',
      user_mobile: user.userMobile,
      user_id: user.userId,
      image_urls: result[0],
      device_object: JSON.stringify(result[1]),
      app_version: result[2].fullVersion,
      app_source: 'super_app',
    });
    if (feedback.image_urls && feedback.image_urls.length) {
      feedback.image_urls = feedback.image_urls.join(';');
    } else {
      feedback.image_urls = '';
    }
    delete feedback.images;
    console.log('submitFeedback:', feedback);
    // 5.调用http保存反馈信息
    return submitFeedback(feedback);
  });
};

/*
 * 上传调试数据文件(仅上传不提交到后台)
 * @returns {promise}
 * */
_self.uploadDebugFile = function() {
  return getFiles().then(function(results) {
    return Promise.resolve(results[0]);
  });
};

_self.feedBack = function(feedback) {
  let defer = promise.defer();
  network.getHasNetwork().then(hasNetwork => {
    if (!hasNetwork) {
      // 1.2.无网络中断
      Vue.prototype.$toast.clear();
      Vue.prototype.$toast('您的当前网络不可用，请检查您的网络设置', 2000);
      return Promise.reject({
        message: '您的当前网络不可用，请检查您的网络设置',
      });
    }
    // Popup.loading('正在提交...');
    return Promise.resolve()
      .then(function() {
        // 上传附件
        return _self.uploadDebugFile().then(function(result) {
          return result || '';
        });
      })
      .then(function(debugUrl) {
        let imgArr = [];
        for (let i = 0; i < feedback.photoList.length; i++) {
          imgArr.push(feedback.photoList[i].img_localpath);
        }

        return _self
          .submitFeedback({
            images: imgArr,
            remark: feedback.remark,
            type: feedback.type,
            debug_file: debugUrl,
          })
          .then(function() {
            Vue.prototype.$toast.clear();
            defer.resolve(
              '感谢您的积极反馈，我们会尽快修复或完善您反馈的相关功能'
            );
          });
      })
      .catch(function(error) {
        Vue.prototype.$toast.clear();
        console.error(error || '提交失败，请稍后重试');
        defer.reject('提交失败，请稍后重试');
      });
  });

  return defer.promise;
};

export default _self;
