const LAST_EXEC_TIME_DATA = 'last_exec_time_data';

let _tempData = {};

/**
 * 获取临时变量
 *
 * @param {*} key
 * @returns
 */
let getItem = key => {
  if (key === null || key === undefined) {
    throw new TypeError('timer: 无效的key');
  }
  return _tempData[key];
};

/**
 * 设置临时变量
 *
 * @param {*} key
 * @param {*} value
 */
let setItem = (key, value) => {
  if (key === null || key === undefined) {
    throw new TypeError('timer: 无效的key');
  }

  if (!value) {
    throw new TypeError('timer: 无效的value');
  }

  _tempData[key] = value;
};

let getNowSeconds = function() {
  return new Date().getTime() / 1000;
};

/**
 * 仅在达到执行间隔后才执行对应方法.
 *
 *  NOTE: 此处使用funcId来区分，如果没有funcId则使用 funcExec函数 的定义字符串作为key来区分不同方法,
 *      多处调用同一方法时需注意字符串格式化的差异(换行\空格\制表符),
 *      这种场景建议采用同一变量
 *
 * @param intervalSeconds {Number} 间隔秒数
 * @param funcExec {Function|Object} 执行方法
 * @param funcId {String} 唯一标识
 * @returns {*}
 */
let execIfAfterInterval = function(intervalSeconds, funcExec, funcId = '') {
  console.log('======= funcExec ======', funcExec);
  let funcKey = funcId || funcExec.toString();
  let lastExecTimeObj = getItem(LAST_EXEC_TIME_DATA) || {};

  // 第一次不执行，默认置为当前时间
  if (!lastExecTimeObj || !lastExecTimeObj[funcKey]) {
    // 保存本次执行时间
    lastExecTimeObj[funcKey] = getNowSeconds();
    setItem(LAST_EXEC_TIME_DATA, lastExecTimeObj);
    return;
  }

  let lastExecSeconds;
  let returnValue;

  // 未达到执行间隔不执行
  lastExecSeconds = lastExecTimeObj[funcKey];
  if (lastExecSeconds && getNowSeconds() - lastExecSeconds < intervalSeconds) {
    return;
  }

  // 达到执行间隔后执行
  returnValue = funcExec();
  console.log('returnValue =======> ', returnValue);

  // 保存本次执行时间
  lastExecTimeObj[funcKey] = getNowSeconds();
  setItem(LAST_EXEC_TIME_DATA, lastExecTimeObj);

  // 返回值
  return returnValue;
};

export { execIfAfterInterval };
