<template>
  <div class="wrapper" v-if="isInit">
    <div class="main">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <!-- <app-footer
      :items="menuItems"
      :active-index="activeMenuIndex"
      @onSelect="change"
    /> -->
  </div>
</template>

<script>
import { menuItems } from '@config/menu';
// import AppFooter from '@components/footer';
import TimingService from '@src/services/timing-service';
import { mapState } from 'vuex';

let init = false;

export default {
  name: 'Layout',
  components: {
    // AppFooter,
  },
  data() {
    return {
      // 底部菜单项
      menuItems: menuItems,
      // 初始时被激活的菜单索引
      activeMenuIndex: 0,
    };
  },
  computed: {
    ...mapState({
      isInit: state => state.app.isInit,
    }),
  },

  watch: {
    $route: function() {
      this.setIndex();
    },
  },

  created() {
    if (!init) {
      this.setIndex();
    }
  },

  methods: {
    setIndex() {
      const name = this.$route.name;
      // 当路由被改变时，修改导航菜单的激活项
      this.activeMenuIndex = this.menuItems.findIndex(item => {
        return (
          (item.routeName && item.routeName === name) ||
          (Array.isArray(item.subs) && item.subs.indexOf(name) > -1)
        );
      });
    },

    change() {
      TimingService.doUpdate();
    },
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;

  .main {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
}
</style>
