import ajax from '@src/common/ajax';
import { getToken } from '@yl-app-jssdk/context';
import { getUserContext } from '@yl-app-jssdk/context';

// 获取todo列表
export const getTodoList = (url, group) => {
  const params = {
    unified_token: getToken(),
    _t: Math.random(),
    access_token: getToken(),
    __from: 'super_app',
    __YL_SUPER_APP__: 1,
    tenant_code: getUserContext().tenantCode,
  };
  if (group === 'zhkf') {
    params.app_code = 'AppEngineerKit';
  }
  return ajax({
    url,
    method: 'get',
    data: params,
    token: false,
  })
    .then(res => {
      // 工程协同返回结果处理
      if (group === 'gcxt') {
        if (!res.success || !res.data) {
          return Promise.reject(res.message);
        }
        let result = {};
        Object.keys(res.data).forEach(v => {
          result[v] = Object.assign({}, res.data[v], {
            params: { module_key: res.data[v].module_key },
          });
        });
        return result;
      }
      // 质检返回结果处理
      if (group === 'zjapp') {
        const { result } = res;
        return result;
      }
      // 智慧客服返回结果处理
      if (group === 'zhkf') {
        return res.data;
      }

      // 标准返回结果
      if (res.errcode !== 0) {
        return Promise.reject(res.errmsg);
      }
      return res.data;
    })
    .catch(err => {
      throw new Error(err);
    });
};

