/**
 * 获取待办数量
 * @param {url} 接口请求地址（工程协同和质检两个接口）
 * @param {data} 接口请求参数
 * @param {method} 接口请求方法
 */
export const getTodoCount = (url, group = '') => {
  const params = {
    access_token: getToken(),
    unified_token: getToken(),
    __from: 'super_app',
    // o: getUserContext().tenantCode,
    __YL_SUPER_APP__: 1,
    _t: Math.random(),
  };
  if (group === 'zhkf') {
    params.app_code = 'AppEngineerKit';
  }
  return ajax({
    method: 'get',
    url,
    data: params,
  })
    .then(res => {
      // 工程协同待办数量结果
      if (group === 'gcxt') {
        if (!res.success || !res.data) {
          return Promise.reject(res.message);
        }
        return Promise.resolve(+res.data.count);
      }

      // 质检待办数量结果
      if (group === 'zjapp') {
        return Promise.resolve(+res.result.todo_total);
      }

      if (group === 'zhkf') {
        return Promise.resolve(+res.data.count);
      }

      // 标准待办数量结果
      if (res.errcode !== 0) {
        return Promise.reject(res.errmsg);
      }
      return Promise.resolve(+res.data);
    })
    .catch(err => {
      throw new Error(err);
    });
};
