const getManifest = (appPath, bundle = 'app') =>
  new Promise(async (resolve, reject) => {
    // 判断是否开发环境
    const manifestPath = /^\d+$/.test(appPath)
      ? `//${window.location.hostname}:${appPath}/manifest.json`
      : `${appPath}/manifest.json`;
    try {
      const data = await getFile(manifestPath);
      const { entrypoints, publicPath } = data;
      const assets = entrypoints[bundle].assets;

      console.log('获取manifest ===> ', data);
      console.log('assets ====> ', assets);
      console.log('publicPath ====> ', publicPath);
      for (let i = 0; i < assets.length; i++) {
        await runScript(publicPath + assets[i]).then(() => {
          if (i === assets.length - 1) {
            resolve();
          }
        });
      }
    } catch (error) {
      reject(error);
      console.error(error);
    }
  });

export default getManifest;
