import device from '@yl-app-jssdk/device';
import version from './version-update-service';
import context from '@yl-app-jssdk/context';
import router from '@router';
import Vue from 'vue';
import network from '@yl-app-jssdk/network';

let _checkUpdating = false;

function versionUpdate() {
  const user = context.getUserContext();

  if (
    user.tenantCode === 'yltest' &&
    user.phone === '18148515223' &&
    window.GLOBAL_ENV.APP_ENV === 'prod'
  ) {
    return Promise.resolve();
  }

  return network
    .getHasNetwork()
    .then(function(result) {
      if (result) {
        if (_checkUpdating) {
          return Promise.reject('正在检测版本更新');
        }

        // 未登录
        if (!context.getIsLogged()) {
          router.replace('/login');
          return Promise.reject('用户未登录');
        }

        _checkUpdating = true;

        return device.ready().then(() => {
          return version
            .checkAppUpdate(
              window.TENANT_CONFIG.appCode || process.env.VUE_APP_CODE
            )
            .then(result => {
              // 如果有热更新，关闭登录的toast，loading
              Vue.prototype.$toast.clear();
              _checkUpdating = false;
              // 大版本更新
              if (result.type === 'AppVersion') {
                return version
                  .showUpgradePopup(result.updatedVersion)
                  .then(() => {
                    // 点击取消更新大版本，判断是否有热更新
                    if (result.updated && result.updated.hotUpdateVersion) {
                      return version.doUpdate(result.updated.hotUpdateVersion);
                    }
                  });
              } else {
                // 热更新
                return version.doUpdate(result.updated.hotUpdateVersion);
              }
            })
            .catch(reason => {
              _checkUpdating = false;
              // $log(`====版本更新，未执行更新：${reason}====`);
              return Promise.resolve(reason);
            });
        });
      } else {
        return Promise.resolve();
      }
    })
    .catch(err => {
      console.error(err);
      return Promise.resolve();
    });
}

export { versionUpdate };
