import photo from '@src/common/take-photo';
import path from '@yl-app-jssdk/path';
import FeedbackService from '@src/services/feedback-service';
import Utils from '@src/common/utils';
import uuid from '@yl/guid';
import Vue from 'vue';
import { getUserContext } from '@yl-app-jssdk/context';
export default (function() {
  let model = {
    photoList: [],
    remark: '',
    uploadLog: true,
    currType: 'optimumSuggest',
  };

  return {
    model: model,
    actions: [
      {
        name: '拍照',
        index: 0,
      },
      {
        name: '从手机相册选择',
        index: 1,
      },
    ],

    init: imgPath => {
      model.photoList = [];
      model.remark = '';
      model.uploadLog = true;
      model.currType = 'optimumSuggest';

      if (imgPath && imgPath.fullPath) {
        model.photoList.push({
          img_localpath: imgPath.fullPath,
          img_view: imgPath.view_path
            ? imgPath.view_path
            : path.getWebViewPath(imgPath.fullPath),
        });
      }
    },

    removeImg: index => {
      model.photoList.splice(index, 1);
    },

    takePhoto: fromAlbum => {
      // let pathObj = path.getNewProblemImagePath({
      //   imageId: uuid(),
      //   type: 'feedback',
      // });
      let fullPath = path.getPrefixPath('feedback').fullPath + uuid() + '.jpg';
      let pathObj = {
        full_path: fullPath,
        view_path: path.getWebViewPath(fullPath),
      };
      let userName = getUserContext().user_name;
      let dateTime = Utils.getNowDate();

      let options = {
        withGraffiti: true,
        text: userName + ' ' + dateTime + ' ' + '拍摄',
      };
      if (fromAlbum) options.fromAlbum = fromAlbum;

      return photo
        .takePhoto(options, pathObj.full_path)
        .then(() => {
          model.photoList.push({
            img_localpath: pathObj.full_path,
            img_view: pathObj.view_path,
          });
        })
        .catch(error => {
          // 没有相机权限弹出提示
          if (error.notAllow) {
            return Promise.reject('请开启对 相机/相册 的访问权限');
          }
        });
    },

    submit: () => {
      if (model.remark === '') {
        return Promise.reject('请输入意见反馈');
      } else if (model.remark.length > 200) {
        return Promise.reject('字数不能超过200字');
      }

      Vue.prototype.$toast.clear();
      Vue.prototype.$toast.loading({
        mask: true,
        message: '正在提交...',
        duration: 0,
      });

      return FeedbackService.feedBack({
        photoList: model.photoList,
        remark: model.remark,
      });
    },

    showLargeImg: index => {
      let imgArr = [];

      model.photoList.forEach(v => {
        if (v.img_localpath.indexOf('file:///') == -1) {
          imgArr.push(`file://${v.img_localpath}`);
        } else {
          imgArr.push(v.img_localpath);
        }
      });

      Utils.openImageBrowser(imgArr, index);
    },
  };
})();
