<template>
  <div
    id="super-app"
    class="custom-theme"
    :class="{ ios: isIOS, iosx: isIOSX }"
  >
    <keep-alive>
      <router-view />
    </keep-alive>
    <!-- 右上角5秒内连续点击10次后进入调试页面 -->
    <div class="debugger" @click="onDebugger"></div>
  </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
  data() {
    return {
      debuggerTimer: null,
      debuggerClickNum: 0,
    };
  },
  computed: {
    ...mapState({
      isIOSX: state => state.app.global.isIOSX,
      isIOS: state => state.app.global.isIOS,
    }),
  },
  methods: {
    onDebugger() {
      if (!this.debuggerTimer) {
        this.debuggerClickNum = 0;
        this.debuggerTimer = setTimeout(() => {
          this.debuggerTimer = null;
        }, 3000);
      } else {
        this.debuggerClickNum++;
        if (this.debuggerClickNum == 10) {
          this.debuggerClickNum = 0;
          this.$router.push('/debugger');
        }
      }
    },
  },
};
</script>
