import router from '@router';
// import LoginService from '@services/login-service';
import Log from '@src/common/log';
import version from '@src/common/version';
import UserService from '@src/services/user-service';
import PageRefreshService from '@src/services/page-refresh-service';

// const VERSION = {
//   zjapp: {
//     version: '3.1.5',
//     webVersion: '40',
//     fullVersion: '3.1.553',
//   },
// };

window.$log = Log;

window.__YL_SUPER_APP__ = true;

window.__YL__ = {
  ENV: window.GLOBAL_ENV.APP_ENV,
  Log,
  router: {
    home: () => {
      router.replace({ name: 'workbench' });
    },
    todo: () => {
      router.replace({ name: 'todo' });
    },
    me: () => {
      router.replace({ name: 'user' });
    },
  },
  // logout: () => {
  //   console.log('调用退出登录操作：', window.location.href);
  //   // return LoginService.logout();
  //   return () => {};
  // },
  backFuncs: {}, // 储存返回方法
  logoutFuncs: {},
  /**
   * 自动重新登录
   * @params {string} [code] 模块名称
   * @params {function} [func] 回调函数
   * @returns {promise}
   */
  registerBackbutton(code, func) {
    this.backFuncs[code] = func;
  },
  magicWindowFuncs: {},
  registerMagicWindowCB(code, func) {
    this.magicWindowFuncs[code] = func;
  },
  // 各个模块插件注册退出登录执行事件
  registerLogout(code, func) {
    this.logoutFuncs[code] = func;
  },
  // 如果有传入code，返回插件模块的版本，如果没有，则返回超级app的版本信息
  getVersion() {
    // if (code) {
    //   return Promise.resolve(VERSION[code] || '');
    // }
    // 超级app的版本信息
    return version.getAppFullVersion().then(res => {
      return {
        version: res.appVersion,
        webVersion: res.webVersion,
        fullVersion: res.fullVersion,
      };
    });
  },
  refreshToken: () => UserService.refreshToken,

  workbenchRefresh: () => PageRefreshService.workbench,
};
