import ajax from '@src/common/ajax';
import { getToken } from '@yl-app-jssdk/context';
// import MessagePush from '@yl-app-jssdk/message-push';

const domain = window.GLOBAL_ENV.SSO_API;

const getConfig = (url, data = {}, method = 'get') => {
  let token = getToken();
  return {
    method,
    url: `${domain}${url}`,
    data: Object.assign(data, {
      accessToken: token,
    }),
  };
};

const formatResult = result => {
  if (!result || result.errcode) {
    // if (result.errcode === 10021 || ~result.message.indexOf('过期')) {
    //   // Vue.prototype.$toast('登录超时，请重新登录');
    //   // return loginService.doLogout();
    //   // router.replace('/login');
    // } else {
    //   // Vue.prototype.$toast('请求失败，请稍后重试');
    // }
    return Promise.reject(result.message || result.errmsg || '');
  }
  const { data } = result;
  if (data) {
    return Promise.resolve(data);
  }
  return Promise.resolve(result);
};

/**
 * 修改密码
 * @param {*} data
 */
export const updatePwd = data => {
  return ajax(getConfig('/v2/user/change-password', data, 'POST')).then(res => {
    return formatResult(res);
  });
};

/**
 * 登出
 * @param {*} tenantCode
 * @param {*} userCode
 * @param {*} accessToken
 */
export const logout = () => {
  return ajax({ ...getConfig('/v2/user/logout'), retry: false }).catch(err => {
    console.error(err);
    return Promise.resolve();
  });
};

export const uploadAvatar = data => {
  return ajax(getConfig('/v1/user/upload-avatar', data, 'post'))
    .then(res => {
      return formatResult(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
};

export const getUserInfo = function() {
  return ajax(getConfig('/v2/user/info')).then(res => {
    return formatResult(res);
  });
};

export const preQrLogin = accessid => {
  return ajax({
    method: 'post',
    url: `${window.GLOBAL_ENV.APP_API_URL.GCXT}/superapp/user/pre-login-by-qr-set`,
    data: {
      accessid,
      __YL_SUPER_APP__: 1,
      __from: 'super_app',
      access_token: getToken(),
    },
  }).then(res => {
    if (res.success) {
      return Promise.resolve(res.data);
    } else {
      return Promise.reject(res.message);
    }
  });
};

/**
 * 扫码登录
 * @param {*} data
 */
export const qrLogin = code => {
  return ajax({
    method: 'post',
    url: `${window.GLOBAL_ENV.APP_API_URL.GCXT}/user/login-confirm`,
    data: {
      auth_code: code,
      __YL_SUPER_APP__: 1,
      __from: 'super_app',
      access_token: getToken(),
    },
  }).then(result => {
    if (result.success == 1) {
      return Promise.resolve('登录成功');
    } else {
      return Promise.reject(result.message);
    }
    // return result;
  });
};

export const bindDevice = data => {
  return ajax(getConfig('/v1/device/bind', data, 'post'))
    .then(res => {
      return formatResult(res);
    })
    .catch(err => {
      console.error('绑定设备失败', err);
      return Promise.resolve();
    });
};
