import Vue from 'vue';
import '@src/config/env';
import App from './App.vue';
import router from './router';
import store from './store';
import singleSpaConfig from './single-spa-config';
import AppInit from '@config/init';
// 引入vant
import Vant from 'vant';
import shakePopup from '@components/shake-popup';
import AppHeader from '@components/app-header';
import AppContainer from '@components/app-container';
import EmptyPage from '@components/empty-page';
import fab from '@yl/float-active-button';

import 'vant/lib/index.css';

// 初始化http

// 初始化摇一摇组件
Vue.use(Vant)
  .use(shakePopup)
  .use(EmptyPage)
  .use(AppHeader)
  .use(AppContainer)
  .use(fab, router);

// 阻止启动生产消息
Vue.config.productionTip = false;
// 注册single-spa插件模块，并启动single-spa
singleSpaConfig.registerModule();

AppInit.init();

// 修改vant toast默认配置，对所有Toast生效。
// 初始化弹框时，可能会出现多个弹框，多个遮罩层，隐藏其他弹框的遮罩层，结合样式做兼容
// Vue.prototype.$toast.setDefaultOptions({
//   onClose: () => {
//     const superInitToast = document.querySelector('.super-init-toast');
//     superInitToast && superInitToast.classList.remove('super-init-toast');
//   },
// });

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#super-app');
