import promise from './defer';
import { isIOS } from '@yl-app-jssdk/platform';

export const image2base64 = path => {
  if (window.MUtils && window.MUtils.imageToBase64String) {
    let defer = promise.defer();

    if (!isIOS()) {
      path = path.replace('file://', '');
    }

    window.MUtils.imageToBase64String(
      path,
      result => {
        result = result.replace(/\n/g, '');
        return defer.resolve(`data:image/jpeg;base64,${result}`);
      },
      err => {
        return defer.reject(err);
      }
    );

    return defer.promise;
  }

  return Promise.reject('无法转成base64');
};
