<template>
  <div v-show="show" class="popup-progress-wrapper">
    <div class="popup-progress-inner">
      <span>{{ title }}</span>
      <div class="popup-progress-process">
        <div class="popup-progress-line">
          <div class="popup-progress-complete" :style="{ width: width }"></div>
        </div>
        <span v-if="percent">{{ width }}%</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '',
    },
    show: {
      type: Boolean,
      default: false,
    },
    process: {
      type: Number,
      default: 0,
    },
    percent: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {};
  },

  computed: {
    width() {
      return `${this.process > 100 ? 100 : this.process}%`;
    },
  },
  watch: {
    process(val) {
      if (val === 100) {
        this.finish();
      }
    },
  },
  methods: {
    finish() {
      this.show = false;
    },
  },
};
</script>

<style lang="scss">
.popup-progress-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 102;

  .popup-progress-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 214px;
    height: 70px;
    margin: -35px 0 0 -107px;
    padding: 12px 15px 0;
    font-size: 14px;
    text-align: center;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 4px;

    .popup-progress-process {
      display: flex;
      padding-top: 2px;
      font-size: 12px;
      justify-content: space-between;
      align-items: center;
    }
    .popup-progress-line {
      width: 100%;
      height: 8px;
      margin-top: 10px;
      background-color: #7e7e7e;
      border-radius: 100px;
      overflow: hidden;

      .popup-progress-complete {
        width: 50%;
        height: 100%;
        background-color: #e2e2e2;
        border-radius: 100px;
        transition: width 0.6s;
      }
    }
  }
}
</style>
