<style lang="scss" scoped>
.process {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1000;
  top: 0;
  left: 0;
  text-align: center;
  * {
    box-sizing: border-box;
  }
  &-modal {
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    height: 100%;
    width: 100%;
  }
  &-inner {
    padding: 4.8% 6%;
    position: absolute;
    width: 78.67%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 2px;
    font-size: 12px;
  }
  &-bar {
    padding-bottom: 2%;
    margin: 4% 0;
    position: relative;
    &-bg {
      height: 100%;
      width: 100%;
      border-radius: 20px;
      background: #ebebeb;
      position: absolute;
    }
    &-line {
      height: 100%;
      border-radius: 20px;
      position: absolute;
      background: linear-gradient(90deg, #2171ff 0%, #00ccff 100%);
    }
  }

  &-text {
    line-height: 1;
    color: #666;
    font-size: 12px;
  }
}
</style>

<script>
export default {
  name: 'ProcessBar',
  props: {
    title: {
      type: String,
      default: '',
    },
    show: {
      type: Boolean,
      default: false,
    },
    process: {
      type: Number,
      default: 0,
    },
    percent: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  computed: {
    width() {
      return `${this.process > 100 ? 100 : this.process}%`;
    },
  },
  watch: {
    process(val) {
      if (val === 100) {
        this.finish();
      }
    },
  },
  methods: {
    close() {
      // this.$emit('close');
    },
    finish() {
      // this.close();
      // this.$emit('finish');
      this.show = false;
    },
  },
};
</script>
<template>
  <div v-if="show" class="process">
    <div class="process-modal"></div>
    <div class="process-inner">
      <span>{{ title }}</span>
      <div class="process-bar">
        <div class="process-bar-bg">
          <div class="process-bar-line" :style="{ width: width }"></div>
        </div>
      </div>
      <div v-if="percent" class="process-text">
        {{ width }}
      </div>
    </div>
  </div>
</template>
