/**
 * 格式化时间差
 * @param milliseconds
 */
export default millisecond => {
  var timediff = Math.floor(millisecond / 1000);
  var leftMillisecond = millisecond - timediff * 1000;
  var units = ['秒', '分', '小时'];
  var result = '';
  for (var i = 0; timediff && i < units.length; ++i) {
    if (i === units.length - 1) {
      result = timediff + units[i] + result;
      break;
    }

    var mod = timediff % 60;
    if (mod > 0) {
      result = mod + units[i] + result;
    }

    timediff = Math.floor(timediff / 60);
  }

  if (leftMillisecond > 0) {
    result = result + leftMillisecond + '毫秒';
  }

  return result;
};
