import Context from '@yl-app-jssdk/context';
import Device from '@yl-app-jssdk/device';
import Version from '@src/common/version';
import Plugin from '@src/common/plugin';
import { bindDevice } from '@src/api/user';
let flag = false;

export default {
  getBaseBindInfo() {
    return Promise.all([
      Promise.resolve(Device.getDeviceInfo()),
      Plugin.getBindInfo(),
    ])
      .then(results => {
        return {
          deviceId: results[0].uuid,
          os: results[0].platform,
          clientId: results[1].channelId,
        };
      })
      .catch(error => {
        console.error('getBaseBindInfo error', error);
        return Promise.resolve({});
      });
  },

  bind() {
    // 获取APP版本
    // 获取APP设备信息
    Promise.all([
      Version.getAppFullVersion(),
      Promise.resolve(Device.getDeviceInfo()),
      Plugin.getBindInfo(),
    ]).then(results => {
      console.log('bind ======> ', results);

      /**
       * 问题：百度推送第一次有时获取不到channelId，重新杀掉进程再进去就可以
       * 临时方案：若第一次没获取到channelId则重试一次
       */
      if (!flag && !results[2].channelId) {
        flag = true;
        this.bind();
        return;
      }
      // 获取用户信息
      const user = Context.getUserContext();

      const deviceInfo = {
        tenantCode: user.tenantCode,
        appid: window.TENANT_CONFIG.APP_ID,
        unionid: user.unionId,
        deviceId: results[1].uuid,
        os: results[1].platform,
        clientId: results[2].channelId,
        manufacturer: results[1].manufacturer,
        model: results[1].model,
        serial: results[1].serial,
        uuid: results[1].uuid,
        osVersion: results[1].version,
        appVersion: results[0].fullVersion,
      };

      return bindDevice(deviceInfo).catch(err => {
        console.error('绑定设备失败', err);
        return Promise.resolve();
      });
    });
  },
};
