<template>
  <div v-show="show" class="shake-popup">
    <div class="shake-popup-box">
      <div class="top-border">
        <a href="javascript:;" class="shake-popup-close" @click="close"></a>
      </div>
      <div class="shake-popup-content">
        <div class="shake-title">反馈问题</div>
        <div class="content-textarea">
          <text-box
            ref="textbox"
            :remark="model.remark"
            placeholder="请录入您发现的产品错误或产品优化建议"
            @input="updateRemark"
          ></text-box>
          <p>*请不要在此反馈检查结果</p>
        </div>

        <section class="image-list">
          <div
            v-for="(item, index) in model.photoList"
            :key="index"
            class="img-wrapper"
            :class="{ ml20: index > 0 }"
          >
            <img
              :src="item.img_view"
              alt="photo"
              @click="showLargeImg(index)"
            />
            <i class="delete-btn" @click="removeImg(index)"></i>
          </div>

          <div
            v-show="model.photoList.length < 3"
            class="append-btn"
            :class="{ ml20: model.photoList.length > 0 }"
            @click="addImage"
          >
            <img src="@assets/images/user/icon-add.png" alt="add" />
            <!-- <i class="super-app-iconfont super-app-icon-jiahao"></i> -->
          </div>
        </section>

        <div class="type-list">
          问题类型：
          <div
            class="type-item"
            :class="{ active: model.currType === 'productError' }"
            @click="changeType('productError')"
          >
            产品错误
          </div>
          <div
            class="type-item"
            :class="{ active: model.currType === 'optimumSuggest' }"
            @click="changeType('optimumSuggest')"
          >
            优化建议
          </div>
        </div>

        <p v-show="model.currType === 'productError'" class="upload-log">
          <check-box
            name="上传日志，以便帮您排查问题"
            :checked="model.uploadLog"
            @select="changeUploadStatus"
          ></check-box>
        </p>

        <div class="btn-list">
          <div class="cancel" @click="close">取消</div>
          <div class="submit" @click="submit">提交</div>
        </div>
        <p class="tip">可在“我－设置”中关闭摇一摇反馈问题</p>
      </div>
    </div>
    <van-action-sheet
      v-model="showImageAction"
      :actions="actions"
      cancel-text="取消"
      @select="selectAction"
      @cancel="cancelSelect"
    />
  </div>
</template>

<script>
import textBox from '@components/textbox';
import checkBox from '@components/checkbox';
import FeedbackModel from '@src/model/feedback-model.js';

export default {
  components: {
    textBox,
    checkBox,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: true,
    },

    hideFunc: {
      type: Function,
      default() {},
      required: true,
    },
  },

  data() {
    return {
      model: {},
      showImageAction: false,
      actions: [],
    };
  },

  created() {
    console.log('created shake popup');
    this.model = FeedbackModel.model;
    this.actions = FeedbackModel.actions;
    console.log('this.photoList', this.model.photoList);
  },

  methods: {
    // ...mapMutations('app', [APP]),

    updateRemark(val) {
      this.$set(this.model, 'remark', val);
    },

    changeUploadStatus() {
      this.$set(this.model, 'uploadLog', !this.model.uploadLog);
    },

    changeType(type) {
      this.model.currType = type;
    },

    close() {
      this.$refs.textbox.clear();
      typeof this.hideFunc === 'function' && this.hideFunc();
    },

    addImage() {
      this.showImageAction = true;
    },

    removeImg(index) {
      FeedbackModel.removeImg(index);
    },

    showLargeImg(index) {
      FeedbackModel.showLargeImg(index);
    },

    selectAction(item) {
      return FeedbackModel.takePhoto(item.index)
        .then(() => {
          this.showImageAction = false;
        })
        .catch(err => {
          this.$toast(err || '获取照片失败');
        });
    },

    cancelSelect() {
      this.showImageAction = false;
    },

    submit() {
      FeedbackModel.submit()
        .then(msg => {
          this.$toast.clear();
          this.$toast(msg || '提交成功', 1000);
          this.model.remark = '';
          this.model.photoList = [];
          this.model.uploadLog = true;
          this.model.currType = 'optimumSuggest';
          this.close();
        })
        .catch(error => {
          this.$toast.clear();
          this.$toast(error || '提交失败');
        });
    },
  },
};
</script>

