<template>
  <a
    class="app-button gradient-background"
    :class="buttonStyle"
    href="javascript:void(0);"
    v-on="$listeners"
  >
    <slot />
  </a>
</template>

<script>
export default {
  name: 'AppButton',
  props: {
    disabled: Boolean,
    secondary: Boolean,
  },
  computed: {
    buttonStyle() {
      return [this.disabled && 'disabled', this.secondary ? 'secondary' : ''];
    },
  },
};
</script>

<style lang="scss" scoped>
.app-button {
  display: block;
  height: 40px;
  min-width: 120px;
  // padding: 0 40px;
  border-radius: 20px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  color: white;

  &.disabled {
    opacity: 0.5;
  }

  &.secondary {
    background: #ffffff !important;
    color: $color-theme;
    border: 1px solid $color-theme;
  }
}
</style>
