import promise from '@src/utils/defer';
import * as platform from '@yl-app-jssdk/platform';
import { Dialog } from 'vant';
import device from '@yl-app-jssdk/device';

const errorHandler = function(error, fromAlbum = false) {
  if (error && error.errCode === 1100) {
    let message = '',
      appName = window.TENANT_CONFIG.appName,
      isIOS = platform.isIOS();
    if (isIOS && fromAlbum) {
      message = `请在iPhone的“设置-隐私”选项中，允许${appName}访问你的相册`;
    } else if (isIOS && !fromAlbum) {
      message = `请在iPhone的“设置-隐私”选项中，允许${appName}访问你的相机`;
    } else if (!isIOS && fromAlbum) {
      message = `请在设置-应用-${appName}-权限中开启相册权限`;
    } else {
      message = `请在设置-应用-${appName}-权限中开启相机权限`;
    }
    Dialog.alert({ message: message });
  }
  if (error) {
    if (error.errCode === 999) {
      Promise.reject({
        cancel: 1,
      });
      return;
    } else if (error.errCode === 1100) {
      Promise.reject({
        notAllow: 1,
      });
      return;
    }
    error = error.errMsg || error;
  }

  return Promise.reject(error);
};

/**
 * 拍照/从相册选取(支持涂鸦\裁剪)
 * @param savePath
 * @param options
 * @param options.withGraffiti {boolean} 是否带涂鸦(和裁剪不能同时)
 * @param options.fromAlbum {boolean} 是否从相册选取(非拍照)
 * @param options.isCrop {boolean} 是否需裁剪(和涂鸦不能同时)
 * @param options.ratio {number} 清晰度
 * @param options.reqWidth {number} 宽度
 * @param options.reqHeight {number} 高度
 * @param options.watermark {boolean} 是否添加水印
 * @returns {*}
 */
const takePhoto = (options, savePath, processOptions = {}) => {
  // let rat = platform.isIOS() ? 60 : 80;
  let path = savePath;
  options = Object.assign(
    {},
    {
      // ratio: rat,
      // mode: 'fix',
      // rotateEnable: false,
      // reqWidth: 768,
      // reqHeight: 1024,

      // 从相册选择配置
      // customMode: true,
      edit: true,
      limits: [0, 1, 2],
      autoSelected: 0,
      sourceType: 'photo',
    },
    options
  );

  let func, funcName;

  if (options.fromAlbum) {
    path = savePath.split();
    funcName = 'openAlbum';
  } else {
    funcName = 'openCamera';
    options.photoConfig = {
      // 拍照配置
      autoSelected: 0,
      edit: true,
      limits: [0, 1, 2],
    };
  }

  let MImagePicker = window.MImagePicker;
  func = MImagePicker[funcName];
  return device.ready().then(function() {
    let defer = promise.defer();
    func(
      path,
      function(savedPath) {
        if (!savedPath) {
          console.error(funcName + ': savedPath为空');
          defer.reject('拍照路径异常');
        } else {
          // 图片处理
          if (options.watermark) {
            processOptions = Object.assign(
              {},
              {
                watermarkConfig: {
                  textBackground: '#40000000',
                  textAlignment: 'right',
                  textColumns: [
                    {
                      text: options.text || '',
                      textColor: '#FFFFFF',
                    },
                  ],
                },
                processOptions,
              }
            );
          }
          MImagePicker.imageProcess(
            savePath,
            editPath => {
              console.log(editPath + ': 添加水印成功');
              // 使用传过来的savePath,不使用插件的savePath
              console.log('takePhoto: OK', savePath);
              defer.resolve(savePath);
            },
            error => {
              console.error('拍照添加水印失败:', path, error);
              defer.reject('拍照添加水印失败');
            },
            processOptions
          );
        }
      },
      function(error) {
        defer.reject(error);
      },
      options
    );

    return defer.promise;
  });
};

const takeAvatar = (savePath, fromAlbum = false) => {
  let path = savePath;
  let options = {
    sourceType: 'photo',
  };

  let photoConfig = {
    edit: true,
    editConfig: {
      limits: [2],
      autoSelected: 2,
    },
  };

  let func, funcName;

  if (fromAlbum) {
    path = savePath.split();
    funcName = 'openAlbum';
    options = photoConfig;
  } else {
    funcName = 'openCamera';
    options.photoConfig = photoConfig;
  }

  let MImagePicker = window.MImagePicker;
  func = MImagePicker[funcName];
  return device.ready().then(function() {
    let defer = promise.defer();
    func(
      path,
      function(savedPath) {
        if (!savedPath) {
          console.error(funcName + ': savedPath为空');
          defer.reject('拍照路径异常');
        } else {
          // 图片处理
          MImagePicker.imageProcess(
            savePath,
            () => {
              defer.resolve(savePath);
            },
            error => {
              console.error('生成头像失败:', path, error);
              defer.reject('生成头像失败');
            },
            {
              ratio: 0.6,
              width: 200,
              height: 200,
            }
          );
        }
      },
      function(error) {
        errorHandler(error, fromAlbum).catch(err => {
          defer.reject(err);
        });
      },
      options
    );

    return defer.promise;
  });
};

export default {
  takePhoto,
  takeAvatar,
};
