const TOP_ROUTES = ['todo', 'workbench', 'user', 'login'];

/**
 * ios相关变量
 *
 */
let isIosBool = false;
const ios = () => {
  let headerHeight = isIOS() ? (isIOSX() ? 88 : 64) : 44;
  isIosBool = isIOS();
  let clientHeight = document.documentElement.clientHeight;
  let clientWidth = document.documentElement.clientWidth;
  store.commit('app/APP', {
    isIOS: isIOS(),
    isIOSX: isIOSX(),
    headerHeight, // 顶部banner的高度（包含ios顶部空白）
    footerHeight: isIOSX() ? 70 : 50, // 顶部banner的高度（包含ios顶部空白）
    clientHeight,
    clientWidth,
    contentHeight: clientHeight - headerHeight,
  });
};
/**
 * 键盘初始化
 *
 */
const initKeyboard = () => {
  let _cordova = window.cordova;

  // 初始化键盘
  if (_cordova && _cordova.plugins && _cordova.plugins.Keyboard) {
    _cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
    _cordova.plugins.Keyboard.disableScroll(true);
  }

  if (window.KeyboardExt && isIosBool) {
    document.body.addEventListener('focus', () => {
      window.KeyboardExt.hide();
    });
  }

  if (isIosBool) {
    window.MUtils.unableMoveUpAfterKeyboardAppear &&
      window.MUtils.unableMoveUpAfterKeyboardAppear(false);
  } else {
    window.MUtils.setKeyboardModel && window.MUtils.setKeyboardModel(1);
  }

  window.addEventListener('keyboardWillShow', () => {
    if (isIosBool) {
      let activeElement = document.activeElement;
      activeElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  });
};
/**
 * 监听物理键返回
 */
const listenBackButton = () => {
  document.addEventListener('backbutton', function() {
    let Keyboard;
    try {
      Keyboard = window.cordova.plugins.Keyboard;
    } catch (err) {
      Keyboard = {};
    }
    if (Keyboard.isVisible) {
      Keyboard.close();
    } else if (
      router.currentRoute.meta.isNav &&
      TOP_ROUTES.indexOf(router.currentRoute.name) > -1
    ) {
      // Vue.prototype.$dialog
      //   .confirm({
      //     title: '提示',
      //     message: '确认退出应用吗？',
      //   })
      //   .then(() => {
      //     window.navigator.app.exitApp();
      //   });
      closeWebview();
    } else {
      // 触发绑定的返回方法
      let backFunc =
        window.__YL__.backFuncs[
          window.location.hash.replace(/#\/([\w|-]+)\/.*/, '$1')
        ];
      if (backFunc) {
        backFunc();
      } else {
        router.go(-1);
      }
    }
  });
};
/**
 * 设置Http头部
 *
 * @returns
 */
const initHeader = () => {
  return Promise.all([
    version.getAppFullVersion(),
    Promise.resolve(device.getDeviceInfo()),
  ]).then(results => {
    http.setHeader({
      prefix: 'supper_app_',
      version: results[0].fullVersion,
      deviceId: results[1].uuid,
      Accept: 'application/json, text/plain, */*',
      'Accept-Language': 'zh-cn',
    });
  });
};

