const init = async () => {
  await initHeader();
  await device.ready();

  // 注册招商事件监听
  pmcEventBus();

  ios();

  // 初始化天眼
  LogService.init(
    window.TENANT_CONFIG.APP_YY_CODE,
    window.TENANT_CONFIG.APP_PRODUCT_CODE
  );
  // 关闭sentry日志
  if (window.MicCore && window.MicCore.sentryLogSwitch) {
    window.MicCore.sentryLogSwitch(
      true,
      x => {
        console.log('关闭 sentry 成功', x);
      },
      x => {
        console.log('关闭 sentry 失败', x);
      }
    );
  }
  // 初始化键盘
  initKeyboard();
  // 锁定竖屏
  Plugins.setDeviceOrientation();
  // 切换到后台事件
  device.pause(() => {
    store.commit('app/APP', {
      onPause: true,
    });
    console.log('应用切换到后台');
  });
  console.log('==== getIsLogged() ====', getIsLogged());
  // 用户已经登录，且不在登录页面
  if (getIsLogged() && !window.location.hash.startsWith('#/login')) {
    console.log('======= 初始化开始 ======');
    let user = getUserContext();
    // 没有用户缓存或者没有缓存用户id或者没有refreshToken，退回登录页面
    if (!user || !user.id || !user.refreshToken) {
      LoginService.logout();
    } else {
      try {
        // 获取默认租户，顺便更新全局变量
        const tenant = TenantService.getDefaultTenant();

        // 默认租户与当前租户不一致，退出登录
        if (tenant.tenantCode != user.tenantCode) {
          return LoginService.logout();
        }

        localstorage.init(user.tenantCode, user.id);

        // 初始化MMKV
        MMKV.init(user.tenantCode, user.id);

        store.commit('app/INIT_STATUS', true);

        // 先刷新token
        await UserService.refreshToken().then(async () => {
          // 初始化相关内容，
          // 获取用户认证状态，
          // 获取用户相关信息
          await LoginService.initAfterLogin(user);
          store.dispatch('tenantList/init');
        });
      } catch (err) {
        LoginService.logout();
      }
    }
  }

  // 绑定app恢复事件
  device.resume(() => {
    store.commit('app/APP', {
      onPause: false,
    });
    // // 一个小时检查一次更新
    // execIfAfterInterval(3600, () => {
    //   versionUpdate();
    // });

    // // 一个小时刷新一次token
    // execIfAfterInterval(3600, () => {
    //   if (getIsLogged() && window.location.hash !== '#/login') {
    //     UserService.refreshToken();
    //   }
    // });
    TimingService.doUpdate();
    // setTimeout(function() {
    //   console.log('应用重新激活');
    //   // 摇一摇重新激活
    //   shakeInit();
    // }, 0);
  });

  // 初始化推送
  // PushNotifyService.init();

  // 监听物理返回键
  listenBackButton();
  // 初始化魔窗
  // initMagicWindow();
  // 初始化摇一摇
  // shakeInit();
};

