import LogService from '@services/log-service';
import modulesList from '../config/modules';

const keys = Object.keys(modulesList);

const report = function(type, message, data) {
  let code = window.location.hash.replace(/^#\/(\w+)\/.*/, '$1');
  if (!code || !keys.includes(code)) {
    try {
      // 追溯调用栈，获取调用该方法的entry
      const stack = new Error().stack;
      code = stack
        .replace(/[\s\r\n]/g)
        .replace(/.*window.__module_entry_(\w+)__.*/, '$1');
    } catch (e) {
      console.error(e);
    }
  }

  // code不存在，则认为是主应用的
  code = keys.includes(code) ? code : 'main';

  LogService.report(`${code}-${type}: ${message}`, data);
};

function Log(message = '', data = '', type = 'info') {
  if (typeof Log[type] === 'function') {
    return Log[type](message, data);
  } else {
    throw new Error('无效的方法');
  }
}

Log.log = function(message = '', data = '') {
  if (window.GLOBAL_ENV != 'prod') {
    console.log(message, data);
  }
  report('log', message, data);
};

Log.info = function(message = '', data = '') {
  console.info(message, data);
  report('info', message, data);
};

Log.debug = function(message = '', data = '') {
  console.debug(message, data);
  report('debug', message, data);
};

Log.warn = function(message = '', data = '') {
  console.warn(message, data);
  report('warn', message, data);
};

Log.error = function(message = '', data = '') {
  console.error(message, data);
  report('error', message, data);
};

Log.table = function() {
  if (window.GLOBAL_ENV != 'prod') {
    console.table(...arguments);
  }
};

Log.prototype = Log;

export default Log;
