/* 定时器相关服务 */
import { versionUpdate } from '@services/upgrade-service';
import { execIfAfterInterval } from '@src/utils/timer';
import { getIsLogged } from '@yl-app-jssdk/context';
import UserService from '@services/user-service';
import TodoService from '@services/todo-service';

const VERSIN_KEY = 'version_update';
const REFRESH_KEY = 'token_refresh';
const TODO_COUNT_KEY = 'todo_count_refresh';

export default {
  doUpdate() {
    console.log('======= doUpdate ======');
    // 一个小时检查一次更新
    execIfAfterInterval(
      3600,
      () => {
        versionUpdate();
      },
      VERSIN_KEY
    );

    // 一个小时刷新一次token
    execIfAfterInterval(
      3600,
      () => {
        if (getIsLogged() && !window.location.hash.startsWith('#/login')) {
          UserService.refreshToken();
        }
      },
      REFRESH_KEY
    );
    // 一个小时刷新一次待办数量
    execIfAfterInterval(
      600,
      () => {
        TodoService.getTodoCount();
      },
      TODO_COUNT_KEY
    );
  },
};
