import ajax from '@src/common/ajax';

/**
 * 获取云服务用户权限
 * @param {*} appCode
 */
export const getUserPermissions = (appCode = '') => {
  return ajax({
    method: 'POST',
    url: `${window.GLOBAL_ENV.SSO_CENTER_DEFAULT_API}/m/PermissionApply/permission-auth/get-user-permission`,
    data: {
      appCode,
    },
  })
    .then(result => {
      if (result.errcode != 0) {
        return Promise.reject('请求失败');
      }

      return Promise.resolve(result.data);
    })
    .catch(err => {
      return Promise.reject(err);
    });
};
