function Deferred() {
  if (typeof Promise != 'undefined' && Promise.defer) {
    return Promise.defer();
  } else {
    this.resolve = null;
    this.reject = null;

    this.promise = new Promise(
      function(resolve, reject) {
        this.resolve = resolve;
        this.reject = reject;
      }.bind(this)
    );
    Object.freeze(this);
  }
}

export default {
  defer: function() {
    return new Deferred();
  },
};
