import context from '@yl-app-jssdk/context';
import localstorageApi from '@yl/localstorage';
import CONST from '@src/config/const';
import Network from '@yl-app-jssdk/network';
import { relateTenantList } from '@src/api/sso';
import SsoCenterService from './sso-center-service';
import UserService from './user-service';
import LoginService from './login-service';
import STATIC from '@config/static';
import store from '@store';
import router from '@router';

let changing = false;

let formatTenantList = function(list) {
  let tenantList = [];
  let verifyTenantList = [];

  list.forEach(v => {
    // 没有分离审核通过和未审核通过的
    v.approveStatus = Number(v.approveStatus);
    if (v.approveStatus !== STATIC.SSO_APPROVE_STATUS.pass) {
      verifyTenantList.push(v);
    } else {
      tenantList.push(v);
    }
  });
  context.updateUser({ tenantList, verifyTenantList });
  return { tenantList, verifyTenantList };
};

const _self = {
  /**
   * 设置租户列表
   */
  getTenantList(reload = true) {
    if (!reload) {
      const list = context.getUserContext('tenantList') || [];
      if (list.length) {
        return Promise.resolve({ tenantList: list });
      }
    }

    return Network.getHasNetwork().then(hasNetwork => {
      if (hasNetwork) {
        return relateTenantList()
          .then(res => {
            console.log('relateTenantList ====> ', res);
            return formatTenantList(res.tenantList);
          })
          .catch(() => {
            return Promise.resolve({
              tenantList: context.getUserContext('tenantList') || [],
              verifyTenantList:
                context.getUserContext('verifyTenantList') || [],
            });
          });
      } else {
        return {
          tenantList: context.getUserContext('tenantList') || [],
          verifyTenantList: context.getUserContext('verifyTenantList') || [],
        };
      }
    });
  },

  /**
   * 设置默认租户
   *
   * @param {*} tenantCode
   * @param {*} tenantName
   */
  setDefaultTenant(tenantCode, tenantName = '') {
    localstorageApi.set(
      CONST.DEFAULT_TENANT,
      { tenantCode, tenantName }
      // localstorageApi.level.user
    );

    window.TENANT_CONFIG.TENANT_CODE = tenantCode;
    window.TENANT_CONFIG.TENANT_NAME = tenantName;
  },

  /**
   * 获取默认租户
   */
  getDefaultTenant() {
    if (window.TENANT_CONFIG.TENANT_CODE) {
      return {
        tenantCode: window.TENANT_CONFIG.TENANT_CODE,
        tenantName: window.TENANT_CONFIG.TENANT_NAME,
      };
    }

    let tenant = localstorageApi.getObject(
      CONST.DEFAULT_TENANT
      // localstorageApi.level.user
    );

    if (tenant.tenantCode) {
      window.TENANT_CONFIG.TENANT_CODE = tenant.tenantCode;
      window.TENANT_CONFIG.TENANT_NAME = tenant.tenantName;

      return tenant;
    }

    return {};
  },

  setTenantList(list) {
    // 过滤租户
    let { tenantList, verifyTenantList } = formatTenantList(list);
    console.log('tenantList ====> ', tenantList);

    if (!Array.isArray(tenantList) || !tenantList.length) {
      if (!verifyTenantList.length) {
        // 没有租户列表，打开绑定页面
        const url = '/register/success';
        SsoCenterService.openPage(url);
        throw new Error('未绑定建设单位');
      } else {
        // 获取待审核的租户
        // 只有一个待审核的租户，跳到待审核页面
        if (verifyTenantList.length === 1) {
          let tenantCode = verifyTenantList[0].tenantCode;
          UserService.ticket2token(tenantCode, false).then(res => {
            const url = '/auth/auditing';
            SsoCenterService.openPage(url, {
              tenantCode,
              token: res.accessToken,
              tenantName: verifyTenantList[0].tenantName,
            });
          });

          throw new Error('未审核通过');
        } else {
          router.push({ path: '/verify-tenant-list' });
          throw new Error('未审核通过');
        }
      }
    }

    context.updateUser({ tenantList });

    // 只有一个租户且租户审核通过，绑定为默认租户
    if (tenantList.length === 1) {
      this.setDefaultTenant(tenantList[0].tenantCode, tenantList[0].tenantName);

      return tenantList[0].tenantCode;
    }

    return '';
  },

  clearDefaultTenant() {
    // 如果不是单一租户，清除租户信息缓存
    if (!window.GLOBAL_ENV.ONLY_TENANT) {
      localstorageApi.remove(CONST.DEFAULT_TENANT);

      window.TENANT_CONFIG.TENANT_CODE = '';
      window.TENANT_CONFIG.TENANT_NAME = '';
    }
  },

  changeTenant(tenantCode, tenantName = '') {
    // 没有传入tenantCode或者传入当前租户code，不处理
    if (!tenantCode || tenantCode === window.TENANT_CONFIG.TENANT_CODE) {
      return Promise.resolve();
    }

    if (changing) {
      return Promise.reject('正在切换租户');
    }

    const prevTenantCode = window.TENANT_CONFIG.TENANT_CODE;

    changing = true;

    function run(tenantCode) {
      return UserService.token2token(tenantCode)
        .then(() => {
          // 切换默认租户
          _self.setDefaultTenant(tenantCode, tenantName);
          const user = context.getUserContext();
          store.dispatch('app/reset');
          // 执行初始化
          return LoginService.initAfterLogin(user, false).catch(msg => {
            if (msg === '退出登录') {
              return Promise.resolve();
            } else {
              return Promise.reject();
            }
          });
        })
        .then(() => {
          changing = false;
          return Promise.resolve();
        })
        .catch(() => {
          changing = false;
          // 切换失败，切回原来的租户
          return run(prevTenantCode).then(() => {
            return Promise.reject('切换租户失败');
          });
        });
    }

    return run(tenantCode);
  },
};

export default _self;
