<template>
  <van-action-sheet
    v-model="show"
    :actions="list"
    @select="onSelect"
    cancel-text="取消"
    @cancel="onCancel"
  />
</template>

<script>
import { ActionSheet } from 'vant';

export default {
  // props: {
  //   show: {
  //     type: Boolean,
  //     default: false,
  //   },
  // },

  components: {
    [ActionSheet.name]: ActionSheet,
  },

  data() {
    return {
      show: false,
      list: [],
      onClick: null,
    };
  },

  methods: {
    init(options) {
      this.list = options.list;
      this.onClick = options.onClick;
    },

    click(item) {
      if (typeof this.onClick === 'function') {
        this.onClick(item);
      }
    },

    onCancel() {
      this.show = false;
    },

    onSelect(action) {
      const item = action;

      if (typeof this.onClick === 'function') {
        this.onClick(item);
      }
    },
  },
};
</script>

