import ajax from '@src/common/ajax';
import { getToken } from '@yl-app-jssdk/context';

const domain = window.GLOBAL_ENV.APP_API_URL.ZJ;

/**
 * 提交反馈
 * @param feedback
 * @returns {promise}
 */
export const submitFeedback = function(feedback) {
  // return MessagePush.getMPushBindInfo().then(res => {})
  let baseUrl =
    window.GLOBAL_ENV.APP_ENV != 'prod'
      ? 'https://appzj-test.myyscm.com'
      : 'https://appzj.myyscm.com';

  return ajax({
    method: 'post',
    url: `${baseUrl}/feedback/submit?unified_token=${getToken()}`,
    data: feedback,
  }).then(function(result) {
    if (result && result.errmsg) {
      return Promise.reject(result);
    }
    return !!result;
  });
};
/**
 * 获取反馈
 * @param feedback
 * @returns {promise}
 */
export const getFeedbackList = function(feedback) {
  return ajax({
    method: 'get',
    url: `${domain}/webapi/app-feedback/list?unified_token=${getToken()}`,
    data: feedback,
  }).then(function(result) {
    if (result && result.errmsg) {
      return Promise.reject(result);
    }
    return result.data;
  });
};
