// fab初始化初始化活动窗口
function fab(appCode, tenantCode, UserCode) {
  try {
    Vue.prototype.$fab
      .init(appCode, tenantCode, UserCode, {
        defaultShow: true,
        env: window.GLOBAL_ENV.APP_ENV,
        navBar: {
          barColor: '#ffffff',
          barButtonColor: '#000000',
          barTitleColor: '#000000',
          barPosition: 1,
          barFontSize: 18,
        },
        exRoute: [
          '/login', // 登录
          '/tenant-list', // 租户列表
          '/bind-phone', // 绑定手机
          '/verify-tenant-list', // 选择待审核租户
        ],
      })
      .catch(err => {
        console.log(err);
      });
  } catch (err) {
    console.error(err);
  }
}
// 展示热更新的说明
function showUpdateInfo() {
  const _dialog = ShowUpdateInfoDialog();
  const _obj = localStorageApi.getObject('web_version_update_info');
  _obj && _dialog.open(_obj);
  localStorageApi.remove('web_version_update_info');
}

// 退出登录
async function doLogout(logoutTip = '') {
  // 执行各个模块插件的退出时注册的事件
  if (window.__YL__.logoutFuncs) {
    // Object.keys(window.__YL__.logoutFuncs).map(item => {
    //   try {
    //     window.__YL__.logoutFuncs[item] && window.__YL__.logoutFuncs[item]();
    //   } catch (err) {
    //     console.log('插件模块退出操作出错：', err);
    //   }
    // });

    for (let key in window.__YL__.logoutFuncs) {
      try {
        if (window.__YL__.logoutFuncs[key]) {
          await window.__YL__.logoutFuncs[key]();
        }
      } catch (err) {
        console.log('插件模块退出操作出错：', err);
      }
    }
  }

  if (!window.location.hash.startsWith('#/login')) {
    router.replace({
      path: '/login',
      query: { logoutTip: logoutTip },
    });

    TenantService.clearDefaultTenant();
  }

  // 执行公共的退出
  context.logout();
  context.updateUser({
    tenantCode: '',
    tenantId: '',
    tenantName: '',
    ticket: '',
  });

  store.dispatch('app/reset');
}

/**
 * 登录后初始化
 *
 * @param {*} user 用户信息
 * @param {*} checkUpdate 是否执行版本更新校验，默认true
 */
const initAfterLogin = (user, checkUpdate = true) => {
  //appStore: 账号特殊处理
  if (
    user.tenantCode === 'yltest' &&
    user.phone === '18148515223' &&
    window.GLOBAL_ENV.APP_ENV === 'prod'
  ) {
    //appStore: 设置特殊账号标识
    store.commit('app/APP_STORE_ACCOUNT', true);
    //appStore: 直接写死数据
    return MockService.init(user).then(() => {
      Vue.prototype.$toast.clear();
      router.replace('/workbench');
    });
  }

  // 展示热更新的内容
  showUpdateInfo();

  store.commit('app/APP_STORE_ACCOUNT', false);

  return UserService.getStateInfo()
    .then(res => {
      if (res && res.noPermission) {
        // 如果没有权限，直接返回
        return Promise.resolve(res);
      }
      return UserService.getUserInfo().then(id => {
        localStorageApi.init(user.tenantCode, id || user.id);
        DeviceService.bind();

        http.setHeader({
          userId: id,
          tenantCode: user.tenantCode,
        });

        // try {
        //   LogService.registTracker(user.tenantCode, user.account);
        // } catch (err) {
        //   console.log('天眼初始化失败', err);
        // }

        // 是否需要校验更新
        if (checkUpdate) {
          // 检查版本更新
          return versionUpdate();
        } else {
          return Promise.resolve();
        }
      });
    })
    .then(initResult => {
      Vue.prototype.$toast.clear();
      // 如果未审核，或者正在审核中，不跳转工作台
      if (initResult && initResult.noPermission) {
        doLogout();
        return Promise.reject('退出登录');
      }

      store.commit('app/INIT_STATUS', true);

      return ConfigService.getProducts().then(codes => {
        if (codes.length) {
          window.GLOBAL_ENV.PRODUCTS = codes;
        }

        // 跳转工作台，如果是在登录页面，那么需要跳转工作台
        if (window.location.hash.startsWith('#/login')) {
          router.replace('/workbench');
        }

        ConfigService.updatePageConfig(CONST.TODO_PAGE).then(() => {
          // 获取待办数量
          TodoService.getTodoCount();
          // 获取待办列表
          TodoService.getTodoList();
        });

        // 开始加载插件模块，初始化插件模块
        return singleSpaConfig.loader(codes).then(() => {
          // 初始化fab插件
          let user = context.getUserContext();
          fab(window.TENANT_CONFIG.adCode, user.tenantCode, user.id);
        });
      });
    })
    .catch(error => {
      console.error(error);
      store.commit('app/INIT_STATUS', true);
      // $log('登录后初始化失败', error);
      if (!error || !error.handled) {
        return Promise.reject(error || '初始化异常，请稍后重试');
      }
    });
};

const formatLoginInfo = (loginResult, user) => {
  // 如果是第一阶登录
  if (loginResult.ticket) {
    let tenantCode = window.TENANT_CONFIG.TENANT_CODE;
    user.ticket = loginResult.ticket || '';
    // 先设置user信息，否则会导致setTenantList取不到用户信息
    context.setUserContext(user);

    try {
      tenantCode = TenantService.setTenantList(loginResult.tenantList);
    } catch (err) {
      console.log(err);
      tenantCode = '';
      return Promise.reject(err && err.message);
    }

    if (tenantCode) {
      // 如果有默认租户，登录默认租户
      user.tenantCode = tenantCode;
      return UserService.ticket2token(window.TENANT_CONFIG.TENANT_CODE);
    }
  } else {
    user.token = loginResult.accessToken || '';
    user.accessToken = loginResult.accessToken || '';
    user.refreshToken = loginResult.refreshToken || '';
  }
  context.setUserContext(user);
  return Promise.resolve(loginResult);
};

// 登录成功处理
const loginSuccess = loginResult => {
  let user = {
    name: '',
    id: '',
    deviceId: loginResult.device_id,
    account: loginResult.account || loginResult.mobile,
    phone: loginResult.account || loginResult.mobile,
    userPassword: loginResult.password || '',
    tenantCode: loginResult.tenantCode || '',
    tenantId: '',
    ticket: loginResult.ticket || '',
    loginType: 1,
    loginBy: loginResult.loginBy,
  };
  return formatLoginInfo(loginResult, user).then(result => {
    if (result.accessToken) {
      //  登陆后初始化
      return initAfterLogin(user);
    } else if (result.ticket) {
      Vue.prototype.$toast.clear();
      router.replace('/tenant-list');
    } else {
      return Promise.reject({
        ...result,
        known_error: true,
        message: result.msg,
      });
    }
  });
};

export default {
  loginByCode(phone, code, tenantCode = '') {
    const data = {
      loginType: 'MOBILE_AND_SMS',
      appid: window.TENANT_CONFIG.APP_ID,
      mobile: phone,
      smsCode: code,
    };

    tenantCode && (data.tenantCode = tenantCode);

    return this.doLogin(data);
  },

  loginByPwd(account, password, tenantCode = '', loginBy = 'MOBILE') {
    const data = {
      loginType: 'ACCOUNT_AND_PWD',
      appid: window.TENANT_CONFIG.APP_ID,
      account: account,
      password: password,
    };

    tenantCode && (data.tenantCode = tenantCode);

    return this.doLogin(data, loginBy);
  },

  doLogin: (loginData, loginBy) => {
    Vue.prototype.$toast.loading({
      mask: true,
      message: '登录中...',
      duration: 0,
    });

    http.setHeader({
      userId: '',
      tenantCode: '',
    });

    // let user = {};
    return network
      .getHasNetwork()
      .then(hasNetwork => {
        // $log('登录，网络校验', { hasNetwork });
        // 1.网络判断
        if (!hasNetwork) {
          // 1.2.无网络中断
          Vue.prototype.$toast.clear();
          Vue.prototype.$toast('您的当前网络不可用，请检查您的网络设置', 2000);
          return Promise.reject('您的当前网络不可用，请检查您的网络设置');
        }
      })
      .then(() => {
        // todo deviceInfo 可用于获取mpush设备绑定信息用于注册mpush
        // console.log(deviceInfo, 'mpush 设备信息');
        loginData.device_id = (window.device && window.device.uuid) || '';
        // 2.执行登录请求
        return login(loginData);
      })
      .then(loginResult => {
        return loginSuccess({ ...loginData, loginBy, ...loginResult });
      });
  },
  initAfterLogin,

  logout: (logoutTip = '') => {
    return network
      .getHasNetwork()
      .then(function(hasNetwork) {
        // 有网络时先执行远程登出
        // 无网络时记录本次未处理操作
        if (hasNetwork) {
          var user = context.getUserContext();
          return logoutApi(user.tenantCode, user.userCode, user.token).then(
            function(result) {
              // 当执行上次登出失败时，仅记录错误下次登录重新执行，不抛出异常
              if (!result || result.errmsg) {
                // context.saveUnhandledLogout();
                console.error(
                  'logout:' + ((result && result.errmsg) || '登出失败')
                );
              }
            },
            function(error) {
              console.error('logout:error', error);
            }
          );
        }
      })
      .then(function() {
        doLogout(logoutTip);
      })
      .catch(function(error) {
        doLogout(logoutTip);
        console.error('logout:error', error);
      });
  },

  // 单点登录
  singleSignOn(data = {}) {
    const login = () => {
      Vue.prototype.$toast.loading({
        duration: 0, // 持续展示 toast
        overlay: true,
        forbidClick: true, // 禁用背景点击
        loadingType: 'spinner',
        message: '加载中...',
      });
      return network
        .getHasNetwork()
        .then(hasNetwork => {
          if (!hasNetwork) {
            // 1.2.无网络中断
            Vue.prototype.$toast.clear();
            Vue.prototype.$toast(
              '您的当前网络不可用，请检查您的网络设置',
              2000
            );
            return Promise.reject('您的当前网络不可用，请检查您的网络设置');
          }
        })
        .then(() => {
          return singleSignOnApi(data).then(res => {
            console.log('单点登录成功', res);
            context.setUserContext({
              tenantCode: data.tenantCode,
            });
            data.device_id = (window.device && window.device.uuid) || '';
            return loginSuccess({ ...res, ...data });
          });
        })
        .catch(() => {
          Vue.prototype.$toast.clear();
        });
    };
    if (context.getIsLogged()) {
      this.logout()
        .then(() => {
          login();
        })
        .catch(() => {
          login();
        });
    } else {
      login();
    }
  },
};
