import context from '@yl-app-jssdk/context';

// 加载子应用模块白名单
export const loadSubAppWhiteList = {
  zhkf: ['lsfz', 'zzyl', 'retesting', 'cjap', 'cqyl', 'myyl', 'dpzy'],
};

// 是否显示智慧客服模块
export const showZhkfModlue = () => {
  const tenantCode = context.getUserContext('tenantCode');
  return (
    loadSubAppWhiteList['zhkf'].includes(tenantCode) &&
    !!window.__module_entry_zhkf__
  );
};
