import AlertComponent from './alert';
let $vm;
export const hotUpdateAlertInit = Vue => {
  const Alert = Vue.extend(AlertComponent);
  if (!$vm) {
    $vm = new Alert({
      el: document.createElement('div'),
      propsData: {
        text: '',
      },
    });
    document.body.appendChild($vm.$el);
  }
  const hotUpdateAlert = {
    show({
      title = '',
      content = 'example',
      okText = '更新',
      cancelText = '忽略',
      showCancel = true,
      cancel,
      ok = () => {},
    }) {
      $vm.config = {
        title: title,
        content: content,
        okText: okText,
        cancelText: cancelText,
        showCancel,
        cancel: () => {
          $vm.show = false;
          cancel && cancel();
        },
        ok,
      };
      $vm.show = true;
    },
    hide() {
      $vm.show = false;
    },
  };
  // all comComps plugins are included in this.$comComps
  if (!Vue.$comComps) {
    Vue.$comComps = {
      hotUpdateAlert,
    };
  } else {
    Vue.$comComps.hotUpdateAlert = hotUpdateAlert;
  }

  Vue.mixin({
    created: function() {
      this.$comComps = Vue.$comComps;
    },
  });
};
export default {
  install(Vue) {
    hotUpdateAlertInit(Vue);
  },
};
