/** 获取配置 **/

import WorkbenchConfig from '@src/config/page/home-config';
import TodoConfig from '@src/config/page/todo-config';
import MyConfig from '@src/config/page/my-config';
import { getUserPermissions } from './permission-service';
import PageConfigService from '@services/download/page-config-service';
import MMKV from '@yl-app-jssdk/mmkv';
import _ from 'underscore';
import AUTH from '@config/auth';

const defaultConfig = {
  workbench: WorkbenchConfig,
  todo: TodoConfig,
};

export default {
  updatePageConfig: pageType => {
    return PageConfigService.update(pageType).then(configKey => {
      return MMKV.set(`${pageType}-page-config-key`, configKey);
    });
  },

  getPageConfig: pageType => {
    let config = {};
    return MMKV.get(`${pageType}-page-config-key`)
      .then(configKey => {
        return MMKV.get(configKey)
          .then(pageConfig => {
            if (!_.isEmpty(pageConfig)) {
              config = JSON.parse(pageConfig);
            } else {
              config = JSON.parse(JSON.stringify(defaultConfig[pageType]));
            }
            return Promise.resolve(config);
          })
          .catch(err => {
            console.log('读取MMKV失败', err);
            config = JSON.parse(JSON.stringify(defaultConfig[pageType]));
            return Promise.resolve(config);
          });
      })
      .catch(err => {
        console.log('读取MMKV失败', err);
        config = JSON.parse(JSON.stringify(defaultConfig[pageType]));
        return Promise.resolve(config);
      });
  },

  getMyConfig: () => {
    return MyConfig();
  },

  getProducts: () => {
    return getUserPermissions()
      .then(all => {
        if (!all || (Array.isArray(all) && !all.length)) {
          return '';
        }

        let products = [];
        let result = [];
        // 过滤没有权限的产品
        Object.keys(all).forEach(v => {
          let len = 0;
          Object.keys(all[v]).forEach(k => {
            len += k.length;
          });

          len && products.push(v);
        });

        for (let i in AUTH) {
          if (Array.isArray(AUTH[i])) {
            const child = AUTH[i];
            for (let j in child) {
              if (~products.indexOf(child[j])) {
                result.push(i);
                break;
              }
            }
          } else {
            if (~products.indexOf(AUTH[i])) {
              result.push(i);
            }
          }
        }
        return result;
      })
      .catch(error => {
        console.log('获取产品失败', error);
        return Promise.resolve('');
      });
  },
};
