import ConfigService from '@services/config-service';
import HomeService from '@services/home-service';
import TodoService from '@services/todo-service';
import store from '@store';
import CONST from '@config/const';

export default {
  /**
   * 获取工作台配置
   */
  workbench() {
    return ConfigService.updatePageConfig(CONST.WORKBENCH_PAGE).then(() => {
      HomeService.getHomePageConfig()
        .then(config => {
          store.commit('page/SET_WORKBENCH_MODEL', config);
        })
        .catch(err => {
          console.error('workbench init error', err);
          store.commit('page/SET_WORKBENCH_MODEL', {
            topModules: [],
            modules: [],
          });
        });
    });
  },

  /**
   * 获取待办配置列表
   */
  todoCount() {
    return ConfigService.updatePageConfig(CONST.TODO_PAGE).then(() => {
      return TodoService.getTodoCount()
        .then(todoCount => {
          store.commit('todo/SET_TODO_COUNT', todoCount);
        })
        .catch(err => {
          console.error('todocount init error', err);
          store.commit('page/SET_TODO_COUNT', 0);
        });
    });
  },

  /**
   * 获取待办配置列表
   */
  todoList() {
    return ConfigService.updatePageConfig(CONST.TODO_PAGE).then(() => {
      return TodoService.getTodoList()
        .then(config => {
          store.commit('page/SET_TODO_LIST', config);
          return Promise.resolve(config);
        })
        .catch(err => {
          console.error('todolist init error', err);
          // TODO
          store.commit('page/SET_TODO_LIST', {
            list: [],
          });
        });
    });
  },
};
