<template>
  <div class="checkbox" @click="select()">
    <i
      v-show="!checked"
      :style="{ fontSize: iconSize + 'px' }"
      class="super-app-iconfont super-app-icon-duoxuan-weixuanzhuangtai unchecked"
    >
    </i>
    <i
      v-show="checked"
      :style="{ fontSize: iconSize + 'px' }"
      class="super-app-iconfont super-app-icon-shenhetongguo checked"
    >
    </i>
    {{ name }}
    <slot name="icon"></slot>
  </div>
</template>

<script>
export default {
  props: {
    checked: {
      type: Boolean,
      default: false,
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    name: {
      type: String,
      default: '',
    },

    iconSize: {
      type: [Number, String],
      default: 20,
    },
  },

  methods: {
    select() {
      if (!this.disabled) {
        this.$emit('select');
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.checkbox {
  display: inline-block;

  .unchecked {
    color: #bbbbbb;
  }

  .checked {
    color: #0099ff;
  }

  i {
    display: inline-block;
    vertical-align: middle;
    margin-right: 8px;
  }
}
</style>
