export const SET_CURRENT_ITEM = 'SET_CURRENT_ITEM';
export const SET_ITEMS = 'SET_ITEMS';
export const SET_LOAD_STATUS = 'SET_LOAD_STATUS';

export default {
  namespaced: true,
  state: {
    currentItem: {},
    items: [],
    hasLoad: false,
  },
  mutations: {
    [SET_CURRENT_ITEM](state, payload) {
      console.log('设置curritem-----');
      state.currentItem = payload;
    },
    [SET_ITEMS](state, payload) {
      state.items = payload;
    },
    [SET_LOAD_STATUS](state, payload) {
      state.hasLoad = payload;
    },
  },
  actions: {
    init({ commit, state }) {
      commit(SET_LOAD_STATUS, false);
      return Promise.resolve([
        { name: '保利', id: 1 },
        { name: '保利2', id: 2 },
        { name: '保利3', id: 3 },
      ]).then(res => {
        console.log(res, 'tenantList');
        let currItem = {};
        commit(SET_LOAD_STATUS, true);
        if (Array.isArray(res) && res.length > 0) {
          let curr = res.filter(item => item.selected == 1);
          if (curr.length > 0) {
            currItem = curr[0];
          } else {
            currItem = res[0];
          }
          const cacheCurr = state.currItem;
          if (JSON.stringify(cacheCurr) != JSON.stringify(currItem)) {
            commit(SET_CURRENT_ITEM, currItem);
          }
          commit(
            {
              type: 'app/APP',
              enableDeliveryProcess: currItem.enable_delivery_process * 1,
            },
            { root: true }
          );
          commit(SET_ITEMS, res);
        } else {
          commit(SET_CURRENT_ITEM, {});
          commit(
            {
              type: 'app/APP',
              enableDeliveryProcess: 0,
            },
            { root: true }
          );
          commit(SET_ITEMS, []);
        }
        return Promise.resolve(currItem);
      });
    },

    changeTenant({ commit }, currentItem) {
      return Promise.resolve().then(() => {
        console.log('切换项目中', currentItem);
        commit(SET_CURRENT_ITEM, currentItem);
        commit(
          {
            type: 'app/APP',
            enableDeliveryProcess: currentItem.enable_delivery_process * 1,
          },
          { root: true }
        );
        // return downloadUiService.checkDataUpdate('project');
      });
    },
  },
};
