// import device, { isIOS } from '@yl-app-jssdk/device';
import * as singleSpa from 'single-spa';
import './global';
import loadModule from '@src/common/load-module';
import modulesList from './config/modules';
import store from '@store';
import formatTime from '@utils/time/format-time';
import localStorageApi from '@yl/localstorage';
import CONST from '@src/config/const';
import context from '@yl-app-jssdk/context';
import { loadSubAppWhiteList } from '@/src/config/sub-app-white-list';
let t;
/**
 * 动态加载插件模块资源
 * @param {*} code
 * 1. 如果传了code，那么只触发加载该code对应的资源，
 * 2. 如果code是数组则加载数组中的模块， 空数组则表示不加载任何模块
 * 3. 如果不传入code 则加载全部
 */
function loader(code) {
  const loadQueue = [];
  let styleContent = '';

  Object.keys(modulesList).map(item => {
    styleContent += modulesList[item].style;
    // 如果有传code，那么只加载对应的资源，没有code全部加载
    if (!code || code === item || ~code.indexOf(item)) {
      // 给每个子应用申明分配全局变量空间，避免互相影响，例如window.zjapp = {}
      window[item] = {};
      // 状态标示，对应的子应用模块是否能够点击
      store.commit('app/LOADING_STATUS', { [item]: true });
      const tenantCode = context.getUserContext('tenantCode');
      if (loadSubAppWhiteList[item]) {
        if (loadSubAppWhiteList[item].includes(tenantCode)) {
          _load(loadQueue, item);
        }
      } else {
        _load(loadQueue, item);
      }
    }
  });

  const style = document.createElement('style');
  const head = document.getElementsByTagName('head')[0];
  style.type = 'text/css';
  style.innerHTML = styleContent;
  head.appendChild(style);

  // Vue.prototype.$toast.loading({
  //   className: 'super-init-toast',
  //   mask: true,
  //   message: '正在初始化...',
  //   duration: 0,
  // });

  console.log(`开始加载文件插件模块`);
  t = new Date().getTime();
  return Promise.all(loadQueue).then(() => {
    // 关闭加载框
    // Vue.prototype.$toast.clear();
    store.commit('app/CLEAR_LOADING_STATUS');
    // 删除热更新的标示
    localStorageApi.remove(CONST.IS_WEB_VERSION_UPDATE);
    // 加载完毕子应用，修改顶部栏样式
    window.StatusBar && window.StatusBar.styleDefault();
  });
}

function _load(loadQueue, item) {
  loadQueue.push(
    loadModule(modulesList[item].entry)
      .then(() => {
        const _t = formatTime(new Date().getTime() - t);
        console.log(`${modulesList[item].name}文件加载完成，用时：${_t}`);
        // 给子应用分配全局变量空间
        window[item] = {};

        // 是否热更新后的调用
        const isHotUpdate = !!localStorageApi.getString(
          CONST.IS_WEB_VERSION_UPDATE
        );

        // 初始化各自的初始化
        return (
          modulesList[item].init && modulesList[item].init({ isHotUpdate })
        );
      })
      .then(() => {
        store.commit('app/LOADING_STATUS', { [item]: false });
      })
      .catch(err => {
        store.commit('app/LOADING_STATUS', { [item]: false });
        console.error(`插件模块${item}加载失败：`, err);
        return Promise.resolve();
      })
  );
}

/**
 * 注册single-spa模块插件
 */
function registerModule() {
  const html = document.getElementsByTagName('html')[0];
  // 添加报错捕获
  singleSpa.addErrorHandler(err => {
    console.log('singleSpa error ', err);
    console.log('singleSpa error ', err.appOrParcelName);
    console.log(
      'err.appOrParcelName',
      singleSpa.getAppStatus(err.appOrParcelName)
    );
  });
  // 注册插件模块
  Object.keys(modulesList).map(item => {
    // 添加模块挂载节点，防止部分低版本浏览器single-spa无法创建挂载节点
    let div = document.createElement('div');
    div.setAttribute('id', `single-spa-application:${item}`);
    document.body.appendChild(div);

    singleSpa.registerApplication(
      item,
      async () => {
        if (!window[`__module_entry_${item}__`]) {
          await loader(item);
        }
        return window[`__module_entry_${item}__`];
      },
      location => {
        console.log('路由改变：', location);
        html.classList.add('super-app-dom');
        // 判断是哪个插件模块
        if (location.hash.startsWith(`#/${item}`)) {
          html.classList.add(item);
          // 如果不用setTimeout，可能删除class无效
          window.setTimeout(() => {
            html.classList.remove('super-app-dom');
          }, 10);
          return true;
        } else {
          // 回到主应用，修改顶部栏样式
          window.StatusBar && window.StatusBar.styleDefault();
          html.classList.remove(item);
          return false;
        }
      }
    );
  });

  // 启动singleSpa
  singleSpa.start();
}

export default {
  loader,
  registerModule,
};
