import * as todoApi from '@src/api/todo';
// mport todoCountConfig from '@config/api/todo-count';
// import { getToken, getUserContext } from '@yl-app-jssdk/context';
import store from '@store';
import CONST from '@config/const';
import ConfigService from './config-service';
import TodoConfig from '@src/config/page/todo-config';
const approvalConfig = TodoConfig.list['approval'];

const getApprovalItem = () => {
  return todoApi
    .getApprovals()
    .then(res => {
      if (!res.todo_num) {
        return { approval: { name: '认证审批', todo_num: 0 } };
      }
      return { approval: { name: res.name, todo_num: res.todo_num } };
    })
    .catch(() => {
      return Promise.resolve({ approval: { name: '认证审批', todo_num: 0 } });
    });
};

const getTodoList = (url, group = '') => {
  return todoApi
    .getTodoList(url, group)
    .then(res => {
      console.log(`获取${group}待办列表数据======>`, res);
      return res;
    })
    .catch(() => {
      return Promise.resolve();
    });
};

const getTodoCount = (url, group = '') => {
  return todoApi
    .getTodoCount(url, group)
    .then(res => {
      return res;
    })
    .catch(() => {
      return Promise.resolve();
    });
};

export default {
  // 获取待办列表
  getTodoList() {
    // 获取待办页面配置
    return ConfigService.getPageConfig(CONST.TODO_PAGE).then(config => {
      let funcs = [getApprovalItem()];
      config.list.approval = approvalConfig;
      // 是否有产品权限
      console.log(
        'todo待办拥有组别window.GLOBAL_ENV.PRODUCTS',
        window.GLOBAL_ENV.PRODUCTS
      );
      if (window.GLOBAL_ENV.PRODUCTS.length) {
        window.GLOBAL_ENV.PRODUCTS.forEach(v => {
          if (config.apis[v] && config.apis[v].list) {
            funcs.push(getTodoList(config.apis[v].list, v));
          }
        });
      } else {
        Object.keys(config.apis).forEach(v => {
          if (config.apis[v] && config.apis[v].list) {
            funcs.push(getTodoList(config.apis[v].list, v));
          }
        });
      }

      // 获取接口的待办列表
      return Promise.all(funcs)
        .then(result => {
          let todoList = {};
          result.forEach(list => {
            todoList = Object.assign({}, todoList, list);
          });
          return Promise.resolve(todoList);
        })
        .then(res => {
          // 接口数据与配置的待办数据做合并
          let modules = [];
          Object.keys(res).forEach(v => {
            let item = config.list[v];
            if (item) {
              modules.push(Object.assign({}, item, res[v]));
            }
          });
          console.log('待办列表modules', modules);
          return modules;
        });
    });
  },

  // 获取待办数量
  getTodoCount() {
    return ConfigService.getPageConfig(CONST.TODO_PAGE).then(config => {
      // 初始化待办数量
      let todoCount = 0;
      const funcs = [getApprovalItem()];

      // 是否有产品权限
      if (window.GLOBAL_ENV.PRODUCTS.length) {
        window.GLOBAL_ENV.PRODUCTS.forEach(v => {
          if (config.apis[v] && config.apis[v].count) {
            funcs.push(getTodoCount(config.apis[v].count, v));
          }
        });
      } else {
        Object.keys(config.apis).forEach(v => {
          if (config.apis[v] && config.apis[v].count) {
            funcs.push(getTodoCount(config.apis[v].count, v));
          }
        });
      }
      // 获取接口的待办数量
      Promise.all(funcs).then(result => {
        result[0] = +result[0].approval.todo_num;
        result.forEach(v => {
          todoCount += v || 0;
        });
        store.commit('todo/SET_TODO_COUNT', todoCount);
      });
      return Promise.resolve(todoCount);
    });
  },
};
