import component from './progress';
let $vm;

export const popupProgressInit = Vue => {
  const PopupProgress = Vue.extend(component);
  if (!$vm) {
    $vm = new PopupProgress({
      el: document.createElement('div'),
      propsData: {
        text: '',
      },
    });
    document.body.appendChild($vm.$el);
  }

  const popupProgress = {
    show(title, percent = false) {
      $vm.show = true;
      $vm.title = title;
      $vm.percent = percent;
    },
    progress(val) {
      $vm.process = val;
    },
    hide() {
      $vm.show = false;
    },
  };
  // all comComps plugins are included in this.$comComps
  if (!Vue.$comComps) {
    Vue.$comComps = {
      popupProgress,
    };
  } else {
    Vue.$comComps.popupProgress = popupProgress;
  }

  Vue.mixin({
    created: function() {
      this.$comComps = Vue.$comComps;
    },
  });
};

export default {
  install(Vue) {
    popupProgressInit(Vue);
  },
};
