import device from '@yl-app-jssdk/device';
import promise from '@src/utils/defer';
import validate from '@src/utils/validate';
// import { openMWebview } from '@yl-app-jssdk/webview';

export default {
  /**
   * 云推送，绑定设备信息
   */
  getBindInfo: function() {
    let deferred = promise.defer();
    if (window.PushNotification) {
      device.ready().then(function() {
        window.PushNotification.getBindInfo(
          function(data) {
            data =
              data && validate.isObject(data) ? data : JSON.parse(data || '{}');
            deferred.resolve(data || {});
          },
          function(error) {
            deferred.reject(error);
          }
        );
      });

      return deferred.promise;
    } else {
      deferred.resolve({});
    }
  },

  openUrl: function(
    url,
    title,
    options,
    openFunc = null,
    closeCallback = null,
    errFunc = null
  ) {
    function closeWebview(result) {
      console.log('closeWebview :', result);
      if (typeof closeCallback === 'function') {
        closeCallback(result);
      }
      window.closeWebview = null;
    }

    window.closeWebview = closeWebview;

    const base = {
      url,
      navBar: {
        barColor: '#FFFFFF',
        barButtonColor: '#000000',
        barTitle: title,
        barPosition: 1,
      },
      isShowRefresh: false,
      bindCallMethod: 'closeWebview',
    };

    options = Object.assign({}, base, options);

    for (let i in options) {
      if (options[i] === null) {
        delete options[i];
      }
    }

    console.log('options ====> ', options);

    const callback = {
      onOpen: function() {
        console.log('开启webview');
        if (typeof openFunc === 'function') {
          openFunc();
        }
      },
      onClose: function() {
        console.log('关闭webview');
      },
    };

    const error = function(err) {
      console.error(err);
      if (typeof errFunc === 'function') {
        errFunc();
      }
    };

    return window.MWebview.open(
      // adjustSafeArea开启设配全面屏
      { ...options, adjustSafeArea: false },
      callback,
      error
    );
  },

  /**
   * 二维码扫描
   * @param {*} title
   * @param {*} isFlashSwitchShow
   * @param {*} callback
   */
  scanQRCode: (title = '', isFlashSwitchShow = false, callback = null) => {
    if (!window.MBarcode) {
      return Promise.reject('无法启动扫码功能');
    }

    const version = window.cordova.require('cordova/plugin_list').metadata[
      'com.mysoft.mbarcode'
    ];
    return device.ready().then(() => {
      let defer = promise.defer();
      if (version > '1.2.2') {
        window.MBarcode.scanQRCode(
          {
            onCancel: function() {
              // 取消回调
              console.log('onCancel');
              defer.reject('取消扫码');
            },
            onCannotScan: function() {
              // 无法扫描回调
              console.log('onCannotScan');
              callback();
              defer.reject('无法扫描');
            },
            onSuccess: function(result) {
              // 扫描成功
              console.log('scanQRCode onSuccess', result);
              defer.resolve(result);
            },
            onError: function(error) {
              // 失败回调
              console.log('scanQRCode onError', error);
              console.error(error);
              defer.reject(error);
            },
          },
          {
            naviTitle: title,
            isFlashSwitchShow,
          }
        );
      } else {
        let options = {
          title,
          isFlashSwitchShow,
          callback,
        };
        window.MBarcode.scanQRCode(
          result => {
            defer.resolve(result);
          },
          err => {
            console.error(err);
            defer.reject(err);
          },
          options
        );
      }

      return defer.promise;
    });
  },

  setDeviceOrientation() {
    if (window.MUtils && window.MUtils.setDeviceOrientation) {
      window.MUtils.setDeviceOrientation(
        'portrait',
        () => {
          console.log('setDeviceOrientation success');
        },
        error => {
          console.error('setDeviceOrientation error', error);
        }
      );
    }
  },

  /**
   * 保存图片到相册
   * @param {*} url
   */
  savePicToAlbum(url) {
    if (window.MUtils && window.MUtils.savePicToAlbum && url) {
      let defer = promise.defer();
      window.MUtils.savePicToAlbum(
        url,
        function() {
          console.log('图片保存到相册成功');
          defer.resolve();
        },
        function(error) {
          console.error('图片保存到相册失败:', error, url);
          defer.reject(error);
        }
      );

      return defer.promise;
    }
  },
};
