import myWebLogTracker from 'fast-tracker';

export default (function() {
  var reportFunc = null;
  var trackerInstance = null;

  const log = function(msg = '', data = {}) {
    let str = '';

    try {
      str = JSON.stringify(data);
    } catch (err) {
      console.log('转换json失败', err);
      str = '';
    }

    reportFunc && reportFunc(msg, { data: str });
  };

  return {
    init: function(appCode, productCode) {
      trackerInstance = myWebLogTracker({
        app_code: appCode,
        product_code: productCode,
      });

      if (window.__myWebLogTracker__) {
        reportFunc = window.__myWebLogTracker__.report;
      } else if (trackerInstance && trackerInstance.report) {
        reportFunc = trackerInstance.report;
      }
    },

    registTracker: function(tenantCode, userCode) {
      let instance = trackerInstance || window.__myWebLogTracker__ || null;
      instance &&
        instance.registUser({
          tenant_code: tenantCode,
          user_account: userCode,
        });
    },

    report: log,
  };
})();
