import component from './shake-popup';
import FeedbackModel from '@src/model/feedback-model';

let $vm;

const init = Vue => {
  const Popup = Vue.extend(component);

  const popup = {
    onShow: false,
    show(path = {}) {
      FeedbackModel.init(path);
      $vm.show = true;
      this.onShow = true;
    },

    hide() {
      $vm.close();
      $vm.show = false;
      this.onShow = false;
    },
  };

  if (!$vm) {
    $vm = new Popup({
      el: document.createElement('div'),
      propsData: {
        show: false,
        hideFunc: () => {
          $vm.show = false;
          popup.onShow = false;
        },
      },
    });
    document.body.appendChild($vm.$el);
  }

  if (!Vue.$shakePopup) {
    Vue.$shakePopup = popup;
  }
};

export default {
  install(Vue) {
    console.log('init shake popup');
    init(Vue);
  },
};
