import sha1 from './sha1';

function string2Ascii(str) {
  if (!str) {
    return -1;
  }

  const len = str.length;

  if (len === 1) {
    return str.charCodeAt();
  }

  const result = [];

  for (let i = 0; i < len; i++) {
    result.push(str.charCodeAt(i));
  }

  return result;
}

export const createHexSign = function(data) {
  let beforSignStr = data;

  if (typeof data !== 'string') {
    beforSignStr = [];

    let keys = Object.keys(data);

    keys = keys
      .map(v => {
        return {
          key: v,
          val: string2Ascii(v),
        };
      })
      .sort((x, y) => {
        for (let i = 0; i < x.val.length; i++) {
          if (x.val[i] === y.val[i]) continue;

          return x.val[i] - y.val[i];
        }
      });

    keys.forEach(v => {
      beforSignStr.push(`${v.key}=${data[v.key]}`);
    });

    beforSignStr = beforSignStr.join('&');
  }

  return sha1(beforSignStr);
};
