// import OSS from '@yl-app-jssdk/aliyun-oss';
import OSS from '@yl-app-jssdk/minio-oss';
import { getOssConfig, getSTSTokenUrl } from '@api/oss';
import { getUserContext } from '@yl-app-jssdk/context';
import _ from 'underscore';

let getFileNameFromUrl = URL => {
  let reg = new RegExp('[^/]+$');
  return reg.exec(URL);
};
// import ProblemRepository from '@checkroom/repositories/problem-repository';

/**
 * 获取OSS直传配置
 * @returns {*}
 */
let _getStsOssConfig = function() {
  // return HttpExt.wrapPromiseWithAutoReLogin(function() {
  return getOssConfig('minio').then(function(config) {
    config.stsUrl = getSTSTokenUrl('minio');
    return config;
  });
};

/**
 * 服务公开的对象\方法
 */
let _self = {
  /**
   * 上传文件
   * @param filePathArray 文件绝对路径数组
   * @param sub_dir 上传到oss的文件夹
   * @param businessId 业务id，无实际意义
   * @param putObjectName 上传的文件名
   * @returns {*}
   */
  uploadFiles: (filePathArray, sub_dir, businessId, putObjectName) => {
    let stsConfig;
    let result = [];
    if (!filePathArray || filePathArray.length == 0) {
      return Promise.resolve(result);
    }
    // 1. 获取上传配置
    return _getStsOssConfig()
      .then(config => {
        // 2. 收集待上传数据
        stsConfig = config;
        let objects = {};
        let data = [];
        let objectKey;
        const user = getUserContext();

        objects.endPoint = config.endPoint;
        objects.federationAuthUrl =
          config.stsUrl +
          (~config.stsUrl.indexOf('?') ? '&' : '?') +
          `o=${user.tenantCode || window.TENANT_CONFIG.TENANT_CODE}`;
        filePathArray.forEach(filePath => {
          // objectKey = config.parentDir + '/' + sub_dir + '/' + (putObjectName || getFileNameFromUrl(filePath));
          objectKey =
            'mobilecheckquality/upload/' +
            (putObjectName || getFileNameFromUrl(filePath));
          data.push({
            bucket: config.bucketName,
            objectKey: objectKey,
            objectLocalPath: filePath,
          });
        });
        objects.data = data;

        return OSS.uploadFiles(objects, 0, function(notify) {
          if (notify.fileItem) {
            result.push(
              'http://' +
                stsConfig.customDomain +
                '/' +
                notify.fileItem.objectKey
            );
          } else if (notify.objectKey) {
            result.push(
              'http://' + stsConfig.customDomain + '/' + notify.objectKey
            );
          }
        }).then(() => {
          return Promise.resolve(_.uniq(result));
        });
      })
      .catch(error => {
        console.error('upload_feed_back_files', error);
        // window.$log('上传文件错误', error);
        return Promise.reject(error);
      });
  },
};

export default _self;
