<template>
  <!-- 文本框 -->
  <div class="textarea">
    <textarea
      ref="textarea"
      v-model="reason"
      :placeholder="placeholder"
      :maxlength="maxLength"
      @input="sendMsgToParent"
      @blur="blur"
    ></textarea>
    <span v-if="isShowLength">{{ maxLength - reason.length }}</span>
  </div>
</template>

<script>
export default {
  name: 'Textbox',
  props: {
    placeholder: {
      type: String,
      default: '',
    },
    maxLength: {
      type: String,
      default: '200',
    },
    remark: {
      type: String,
      default: '',
    },
    isShowLength: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      reason: '',
    };
  },

  watch: {
    remark: {
      handler(val) {
        if (val !== this.reason) {
          this.reason = val;
          console.log(this.reason);
        }
      },

      immediate: true,
    },
  },

  methods: {
    sendMsgToParent() {
      this.$emit('input', this.reason);
    },
    blur() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },

    /**
     * 供父级调用：this.$refs.textbox.clear();
     */
    clear() {
      this.reason = '';
    },
  },
};
</script>

<style lang="scss" scoped>
@import '~@styles/variable.scss';
.textarea {
  position: relative;
  height: 110px;
  margin-bottom: 8px;
  textarea {
    width: 100%;
    height: 100%;
    border: none;
    padding: 15px;
    color: $color-text;
    font-size: 14px;
    resize: none;
    border-radius: 0;
    background: #fff;
    &::-webkit-input-placeholder {
      color: #a0a0a0;
      font-weight: 400;
      font-size: 14px;
    }
  }
  span {
    position: absolute;
    bottom: 0px;
    right: 14px;
    color: #bbbbbb;
    font-size: 14px;
  }
}
</style>
