/* eslint-disable */
const zhRegExp = /[\u4E00-\u9FA5\uF900-\uFA2D]/g;

// 获取密码安全等级
function getPwdLevel(pwd) {
  // 未输入密码
  if (!pwd) {
    return 0;
  }

  // 密码长度小于8位
  if (pwd.length < 8) {
    return 1;
  }

  var level = 0;

  // 是否包含数字
  /\d/.test(pwd) && level++;

  // 是否包含小写字母
  /[a-z]/.test(pwd) && level++;

  // 是否包含大写字母
  /[A-Z]/.test(pwd) && level++;

  // 是否包含特殊字符
  /[^a-zA-Z0-9]/.test(pwd) && level++;

  // 设置密码等级
  return level > 3 ? 3 : level;
}

export default {
  /**
   * 是否是手机号码
   *
   * @param {string} phone
   * @returns
   */
  isPhoneNum: phone => {
    return /^1\d{10}$/.test(phone);
  },

  /**
   * 是否是邮箱地址
   *
   * @param {string} email
   * @returns
   */
  isEmail: email => {
    return /\w@\w*\.\w/.test(email);
  },

  /**
   * 是否是数字
   *
   * @param {string} num
   * @returns
   */
  isNum: num => {
    return !isNaN(num);
  },

  /**
   * 是否是urk
   *
   * @param {string} URL
   * @returns
   */
  isUrl: URL => {
    return !!URL.match(
      /(((^https?:(?:\/\/)?)(?:[-;:&=\+\$,\w]+@)?[A-Za-z0-9.-]+|(?:www.|[-;:&=\+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[\+~%\/.\w-_]*)?\??(?:[-\+=&;%@.\w_]*)#?(?:[\w]*))?)$/g
    );
  },

  /**
   * 是否是数组
   *
   * @param {*} arr
   * @returns
   */
  isArray: arr => {
    return Array.isArray(arr);
  },

  /**
   * 是否是对象
   *
   * @param {*} obj
   * @returns
   */
  isObject: obj => {
    return Object.prototype.toString.call(obj) === '[object Object]';
  },

  isFunction: func => {
    return typeof func === 'function';
  },

  hasValue: (obj, key) => {
    return obj != null && typeof obj[key] != 'undefined' && obj[key] != null;
  },

  /**
   * 校验密码
   * @param {*} pwd
   */
  checkPwd: pwd => {
    // 替换中文字符
    if (zhRegExp.test(pwd)) {
      pwd = Utils.toCDB(pwd.replace(zhRegExp, '').replace(/\s+/g, ''));
    }

    const level = getPwdLevel(pwd);

    return { level, password: pwd };
  },
};
