import Comp from './selector';
import Vue from 'vue';

let $vm;

if (!$vm) {
  const Selector = Vue.extend(Comp);
  $vm = new Selector({
    el: document.createElement('div'),
  });
  document.body.appendChild($vm.$el);
}

const defaultOptions = {
  list: [],
  onClick: null,
};

const selector = {
  open(options) {
    if (options) {
      options = Object.assign({}, defaultOptions, options);
      $vm.init(options);
    }

    $vm.show = true;
  },

  show() {
    $vm.show = true;
  },

  hide() {
    $vm.show = false;
  },
};

export default selector;
