import ajax from '@src/common/ajax';
import { getUserContext } from '@yl-app-jssdk/context';
import { getToken } from '@yl-app-jssdk/context';

const domain = window.GLOBAL_ENV.APP_API_URL.ZJ;

/**
 * 获取OSS配置
 * @returns {promise}
 */
export const getOssConfig = (type = '') => {
  let params = {};
  let url = `${domain}/param/get-oss-config`;
  if (type) {
    params.type = type;
  }

  return ajax({
    url,
    method: 'get',
    data: {
      ...params,
      unified_token: getToken(),
      _t: Math.random(),
    },
  }).then(function({ data: result }) {
    // 服务器返回已知异常
    if (result && result.errmsg) {
      return Promise.reject(result);
    }

    // 服务器返回未知异常
    if (!result || !result.endPoint) {
      // errorMessage = 'getOssConfig: 服务器返回结果异常';
      // errorRemark = 'result:' + JSON.stringify(result) + ' url:' + url;
      console.error({
        message: 'getOssConfig: 服务器返回结果异常',
        remark: `result:$JSON.stringify(result)} url:${url}`,
      });

      return Promise.reject('服务器返回结果异常');
    }

    return result;
  });
};

/**
 * 获取STS Token
 * @returns {string}
 */
export const getSTSTokenUrl = (type = '') => {
  let user = getUserContext();
  if (type) {
    return `${domain}/param/get-sts-token?unified_token=${user.accessToken}&type=${type}`;
  }

  return `${domain}/param/get-sts-token?unified_token=${user.accessToken}`;
};
