import router from '@router';
import OnlineHelpService from '@services/online-help-service';
import Plugin from '@src/common/plugin';
import context from '@yl-app-jssdk/context';
import Vue from 'vue';
import WebviewService from '@services/webview-service/mcordova-webview-service';

const FUNS = {
  'online-help': OnlineHelpService.open,
};

export default {
  goto: function(item) {
    if (
      window[item.product] &&
      window[item.product].appStatus &&
      !window[item.product].appStatus.status
    ) {
      Vue.prototype.$toast(
        window[item.product].appStatus.message || '玩命开发中'
      );
      return false;
    }

    if (item.params && typeof item.params === 'string') {
      try {
        item.params = JSON.parse(item.params);
      } catch (e) {
        console.log(e);
      }
    }

    if (item.confirm) {
      Vue.prototype.$dialog
        .confirm({
          title: item.confirm.title || '',
          message: item.confirm.message,
        })
        .then(() => {
          item.confirm = null;
          this.goto(item);
        });

      return false;
    }

    if (item.url && item.url.includes('http')) {
      WebviewService.openUrlWebView(item.url, item.type);
      return;
    }

    if (item.router) {
      // if (item.router.includes('http')) {
      //   openUrlWebView(item.router);
      //   return;
      // }
      // 如果有路由
      console.log({
        path: item.router,
        query: item.params || {},
      });
      return router.push({
        path: item.router,
        query: item.params || {},
      });
    }

    if (item.page) {
      const params = item.page.params;
      const token = context.getToken();

      if (token) {
        let url = item.page.url.replace('{token}', token);

        return Plugin.openUrl(url, params.title, params.options);
      }
    }

    if (
      item.func &&
      item.product &&
      window[`__module_entry_${item.product}__`] &&
      window[`__module_entry_${item.product}__`][item.func]
    ) {
      window[`__module_entry_${item.product}__`][item.func](item.params, {
        from: 'app',
      });
    }

    // 指定页面，使用webview打开url
    if (typeof FUNS[item.code] === 'function') {
      return FUNS[item.code](item.params || {});
    }

    return false;
  },
};
