export default {
  /**
   * 消息处理
   * @param {*} appCode
   * @param {*} data
   * @param {*} options
   */
  msgHandler(appCode, data = {}, options = {}) {
    if (
      appCode &&
      window[`__module_entry_${appCode}__`] &&
      window[`__module_entry_${appCode}__`].superAppMsgCenter
    ) {
      window[`__module_entry_${appCode}__`].superAppMsgCenter(data, options);
    } else {
      return false;
    }
  },

  /**
   * 获取反馈文件
   * @param {*} appCode
   */
  getFeedbackFiles(appCode) {
    if (
      appCode &&
      window[`__module_entry_${appCode}__`] &&
      window[`__module_entry_${appCode}__`].getFeedbackFiles
    ) {
      return window[`__module_entry_${appCode}__`]
        .getFeedbackFiles()
        .catch(err => {
          console.error(
            `window[__module_entry_${appCode}__].getFeedbackFiles() 失败`,
            err
          );
          Promise.resolve([]);
        });
    } else {
      return Promise.resolve([]);
    }
  },
};
