import ProgressBarComponent from './progressBar';
let $vm;
export const progressInit = Vue => {
  const ProgressBar = Vue.extend(ProgressBarComponent);
  if (!$vm) {
    $vm = new ProgressBar({
      el: document.createElement('div'),
      propsData: {
        text: '',
      },
    });
    document.body.appendChild($vm.$el);
  }
  const progressBar = {
    show(title) {
      $vm.show = true;
      $vm.title = title;
    },
    progress(val) {
      $vm.process = val;
    },
    hide() {
      $vm.show = false;
    },
  };
  // all comComps plugins are included in this.$comComps
  if (!Vue.$comComps) {
    Vue.$comComps = {
      progressBar,
    };
  } else {
    Vue.$comComps.progressBar = progressBar;
  }

  Vue.mixin({
    created: function() {
      this.$comComps = Vue.$comComps;
    },
  });
};
export default {
  install(Vue) {
    progressInit(Vue);
  },
};
