<template>
  <van-nav-bar
    class="app-header"
    :title="title"
    :left-text="leftText"
    :right-text="rightText"
    :left-arrow="leftArrow"
    :fixed="fixed"
    :border="border"
    :z-index="zIndex"
    @click-left="leftClick"
    @click-right="rightClick"
  >
    <template slot="right">
      <slot name="right"></slot>
    </template>
    <template slot="title">
      <slot name="title"></slot>
    </template>
    <template slot="left">
      <slot name="left"></slot>
    </template>
  </van-nav-bar>
</template>

<script>
import { NavBar } from 'vant';
// import { checkRouter } from '@config/router-check';

export default {
  name: 'AppHeader',
  comments: {
    NavBar,
  },

  props: {
    title: {
      type: String,
      default: '',
    },
    leftText: {
      type: String,
      default: '',
    },
    rightText: {
      type: String,
      default: '',
    },
    leftArrow: {
      type: Boolean,
      default: true,
    },
    fixed: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: false,
    },
    useClickLeft: {
      type: Boolean,
      default: false,
    },
    zIndex: {
      type: Number,
      default: 1,
    },
  },

  methods: {
    leftClick() {
      console.log('left click');
      if (this.useClickLeft) {
        return this.$emit('click-left');
      }
      this.$router.go(-1);
    },
    handleLeftClick() {},
    rightClick(event) {
      this.$emit('click-right', event);
    },
  },
};
</script>

