import ajax from '@src/common/ajax';
// import { getToken } from '@yl-app-jssdk/context';

const domain = window.GLOBAL_ENV.SSO_API;
/**
 * 生成授权临时凭证
 * @param {*} data
 * accessToken （必填）用户登录凭证
 * appid: （必填）授权主应用自己的 AppID
 * responseType: （必填）授权类型，默认填 code
 * scope: （必填）授权作用域，填 app|web
 */
export const getAuthCode = () => {
  const data = {
    appid: window.TENANT_CONFIG.APP_ID,
    responseType: 'code',
    scope: 'web',
  };
  return ajax({
    method: 'get',
    url: `${domain}/v2/oauth2/auth-code`,
    data,
  })
    .then(res => {
      if (res.errcode === 0) {
        return res.data.code;
      } else {
        return Promise.reject(res.message);
      }
    })
    .catch(err => {
      throw new Error(err);
    });
};

