const get = function() {
  if (store.state.app.isAppStoreAccount) {
    return [
      {
        name: '设置',
        icons: {
          outside: 'super-app-icon-setting',
          inside: 'super-app-icon-setting-light',
        },
        router: '/user/setting',
      },
      {
        name: '购卡',
        icons: {
          outside: 'super-app-icon-buy-cart',
          inside: 'super-app-icon-buy-cart-in',
        },
        userType: [-1, 1],
        router: '/gcxt/buy/index',
      },
      {
        name: '充值',
        icons: {
          outside: 'super-app-icon-recharge',
          inside: 'super-app-icon-recharge-in',
        },
        userType: [-1, 1],
        router: '/gcxt/buy/recharge',
      },
    ];
  }

  const userType = Number(Context.getUserContext('userType') || '');

  // 获取用户权限
  const userPermissions =
    localstorageApi.getObject(
      CONST.USER_PERMISSIONS,
      localstorageApi.level.user
    ) || {};

  let list = JSON.parse(JSON.stringify(actions));

  actions.forEach((v, k) => {
    const _modulesList = Object.keys(modulesList);
    const _arr = v.router ? v.router.split('/') : [];
    const _appCode = _arr[0] || _arr[1];

    if (v.userType && !~v.userType.indexOf(userType)) {
      console.log('list ====> ', v, k);
      delete list[k];
    }
    // 如果有权限点控制，且用户存在此权限点，则不删除
    if (v.menucode) {
      if (!userPermissions[CONST.MANAGEMENT_CENTER].includes(v.menucode)) {
        delete list[k];
      }
    }
    // 如果入口调整到子应用，必须要有加载子应用
    // 先判断入口是不是跳子应用，再判断子应用是否有加载
    if (
      _appCode &&
      _modulesList.includes(_appCode) &&
      window.GLOBAL_ENV.PRODUCTS &&
      !window.GLOBAL_ENV.PRODUCTS.includes(_appCode)
    ) {
      delete list[k];
    }
  });

  list = list.filter(v => {
    if (v) {
      return v;
    }
  });

  return list;
};

export default get;
