<template>
  <div class="login-container blue-theme">
    <app-header
      :border="true"
      :useClickLeft="true"
      @click-left="closeWebview"
    />

    <section>
      <div class="login-error">
        <img
          :src="require('@assets/images/common/icon-error.png')"
          class="error-img"
          alt
        />
        <p class="error-text">网络异常，请重新刷新</p>
        <app-button
          slot="button"
          :disabled="loading"
          class="refresh-button"
          @click="doLogin"
        >
          <van-loading v-if="loading" color="#ffffff" size="18px" />

          <p v-else>
            重新刷新
          </p>
        </app-button>
      </div>
    </section>
    <footer class="footer">
      <p class="copyright">
        Copyright &copy;2015-{{ new Date().getFullYear() }} 明源云链 版权所有
      </p>
    </footer>
  </div>
</template>
<script>
import appButton from '@components/app-button/index.vue';
import { closeWebview, reLogin } from '@services/pmc-event-bus';
import { getToken } from '@yl-app-jssdk/context';

export default {
  components: {
    appButton,
  },
  data() {
    return {
      loading: false,
    };
  },
  activated() {
    this.loading = false;
  },
  methods: {
    doLogin() {
      console.log('当前token为===========>', getToken());
      this.loading = true;
      // loginService
      //   .singleSignOn({ token: getToken() })
      //   .then(() => {
      //     this.loading = false;
      //   })
      //   .catch(err => {
      //     this.loading = false;
      //     console.log(err);
      //   });
      reLogin()
        .then(() => {
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
        });
    },
    closeWebview() {
      console.log('============== 隐藏webview ============');
      closeWebview();
    },
  },
};
</script>
