import {
  getUserInfo as getUserInfoApi,
  updatePwd as updatePwdApi,
  uploadAvatar as uploadAvatarApi,
} from '@api/user';
import {
  refreshToken as refreshTokenApi,
  loginByTicket,
  loginByToken,
  resetPassword,
  bindPhoneApi,
} from '@api/sso';
import {
  getBaseInfo as getBaseInfoApi,
  getUserRoleInfo as getUserRoleInfoApi,
  getUserPostInfo as getUserPostInfoApi,
} from '@api/user-state';
import context from '@yl-app-jssdk/context';
import { image2base64 } from '@src/utils/image';
import network from '@yl-app-jssdk/network';
import guid from '@yl/guid';
// import router from '@router';
import SsoCenterService from './sso-center-service';
import LoginService from './login-service';
import TenantService from '@services/tenant-service';

/**
 * 获取用户角色岗位等信息
 */
const getUserRoleInfo = function() {
  return Promise.all([
    getUserRoleInfoApi().catch(() => ''),
    getUserPostInfoApi().catch(() => ''),
  ]).then(results => {
    context.updateUser({
      role: results[0] || '',
      post: results[1] || '',
    });
  });
};

export default {
  /**
   * 获取用户审核信息
   */
  getStateInfo: function() {
    return network.getHasNetwork().then(function(result) {
      if (result) {
        const token = context.getToken();

        if (!token) {
          return Promise.resolve();
        }

        return getBaseInfoApi()
          .then(res => {
            let url = '';

            context.updateUser({
              approvalState: res.approval_state,
              userType: res.user_type,
              tenantName: res.tenant_name,
              tenantInfo: {
                tenantName: res.user_name,
                mobile: res.user_mobile,
              },
            });

            switch (res.approval_state) {
              case -1: // 待申请
                url = '/register/success';
                break;
              case 2: // 审核拒绝
                // 跳到申请详情页面
                url = '/auth/auditing';
                break;
              case 0:
                // 待审核，跳到申请详情
                url = '/auth/auditing';
                break;
              default:
                break;
            }

            if (url) {
              url += `?userType=${res.user_type}`;
              SsoCenterService.openPage(url);
              // 没有权限或者正在审核中
              return Promise.resolve({ noPermission: true });
            }

            return Promise.resolve();
          })
          .catch(error => {
            console.error(error);
            // return Promise.reject(error.message || '获取审核状态失败');
            return Promise.resolve();
          });
      }
    });
  },

  /**
   * 获取用户角色岗位等信息
   */
  getUserRoleInfo,

  /**
   * 获取用户基本信息
   * @returns {*}
   */
  getUserInfo: function() {
    return network
      .getHasNetwork()
      .then(function(result) {
        if (result) {
          return getUserInfoApi().then(function(result) {
            context.updateUser({
              id: result.openId,
              unionId: result.unionId,
              nickname: result.nickname,
              realname: result.realname,
              username: result.username,
              userId: result.openId,
              userCode: `${result.mobile}`,
              userName: result.username,
              userMobile: result.mobile,
              avatarUrl: result.avatarUrl || '',
              avatarPath: '',
              mobile: result.mobile,
            });
            // 单点登录的用户没有输入账号，所以在这设置
            if (!context.getUserContext('account')) {
              context.setUserContext({
                account: result.mobile + '',
                phone: result.mobile + '',
              });
            }

            getUserRoleInfo();

            return result.openId;
          });
        }

        return context.getUserContext().id;
      })
      .catch(err => {
        console.error(err);
        return Promise.resolve();
      });
  },

  resetPwd: function(countryCode, mobile, smsCode, password) {
    countryCode = countryCode.replace('+', '');

    const data = {
      countryCode,
      mobile,
      smsCode,
      password,
      timestamp: +new Date(),
      nonce: guid(),
    };

    return network.getHasNetwork().then(function(result) {
      if (result) {
        return resetPassword(data)
          .then(() => {
            return Promise.resolve();
          })
          .catch(err => {
            console.error(err);
            return Promise.reject('修改密码失败，请稍后重试');
          });
      } else {
        return Promise.reject('当前网络无法使用');
      }
    });
  },

  /**
   * 修改密码
   * @param oldPwd
   * @param newPwd
   * @param reNewPwd
   * @returns {*}
   */
  updatePwd: function(oldPwd, newPwd, reNewPwd) {
    return network.getHasNetwork().then(function(result) {
      if (result) {
        const data = {
          type: 'CHECK_PASSWORD',
          oldPwd: oldPwd,
          newPwd: newPwd,
          reNewPwd: reNewPwd,
        };
        return updatePwdApi(data)
          .then(function() {
            context.updateUser({
              userPassword: newPwd,
            });

            return Promise.resolve();
          })
          .catch(err => {
            console.error('修改密码失败', err);
            return Promise.reject(err || '修改密码失败');
          });
      } else {
        return Promise.reject('当前网络无法修改密码');
      }
    });
  },

  refreshToken() {
    // 无网情况下不处理刷新token的操作
    return network.getHasNetwork().then(function(hasNetwork) {
      // 有网络时先执行远程登出
      // 无网络时记录本次未处理操作
      if (hasNetwork) {
        const refreshToken = context.getUserContext('refreshToken');

        if (!refreshToken) {
          LoginService.logout();
        }

        const data = {
          refreshToken,
          nonce: guid(),
          timestamp: +new Date(),
        };

        return refreshTokenApi(data)
          .then(res => {
            context.setUserContext({
              token: res.accessToken,
              accessToken: res.accessToken,
              refreshToken: res.refreshToken,
            });

            return Promise.resolve(res.accessToken);
          })
          .catch(err => {
            LoginService.logout();
            return Promise.reject(err);
          });
      }
    });
  },

  ticket2token(tenantCode = '', updateUser = true) {
    if (!tenantCode) {
      return Promise.reject('缺少租户code');
    }

    let ticket = context.getUserContext('ticket');

    if (!ticket) {
      // 没有ticket需要重新登陆
      LoginService.logout();
    }

    const data = {
      ticket,
      tenantCode,
      timestamp: +new Date(),
      nonce: guid(),
    };

    return loginByTicket(data)
      .then(res => {
        if (updateUser) {
          context.setUserContext({
            tenantCode,
            tenantId: '',
            token: res.accessToken,
            accessToken: res.accessToken,
            refreshToken: res.refreshToken,
          });

          TenantService.setDefaultTenant(tenantCode);
        }

        return res;
      })
      .catch(err => {
        LoginService.logout();
        return Promise.reject(err);
      });
  },

  /**
   * token换token（当前token + 新租户code => 新租户的token）
   */
  token2token(tenantCode = '', updateUser = true, token = '') {
    if (!tenantCode) {
      return Promise.reject('缺少租户code');
    }

    const user = context.getUserContext();

    // 租户code不变，不处理
    if (tenantCode === user.tenantCode) {
      return Promise.resolve(user.token);
    }

    if (!user.token && !token) {
      // 没有token需要重新登陆
      return LoginService.logout();
    }

    const data = {
      accessToken: token || user.token,
      tenantCode,
      timestamp: +new Date(),
      nonce: guid(),
    };

    return loginByToken(data)
      .then(res => {
        if (updateUser) {
          context.setUserContext({
            tenantCode,
            tenantId: '',
            token: res.accessToken,
            accessToken: res.accessToken,
            refreshToken: res.refreshToken,
          });

          TenantService.setDefaultTenant(tenantCode);
        }

        return res.accessToken;
      })
      .catch(err => {
        console.error('切换租户失败', err);
        return Promise.reject(err);
      });
  },

  /**
   * 上传头像
   */
  uploadAvatar: filePath => {
    return image2base64(filePath).then(res => {
      console.log('===== uploadAvatar ========');
      return uploadAvatarApi({
        type: 'BASE64_DATA',
        avatarData: res,
      }).then(data => {
        return data.avatarUrl;
      });
    });
  },

  /**
   * 绑定手机号码
   */
  bindPhone: data => {
    data = {
      ...data,
      // ticket: （必填）临时登录凭证,
      // countryCode: （必填）手机区号,
      // mobile: （必填）手机号,
      // smsCode: （必填）短信验证码,
      appid: window.TENANT_CONFIG.APP_ID,
      nonce: guid(),
      timestamp: +new Date(),
    };

    return bindPhoneApi(data).catch(err => {
      return Promise.reject(err);
    });
  },
};
