import Plugin from '@common/plugin';
import context from '@yl-app-jssdk/context';
import qs from 'qs';
import LoginService from '@services/login-service';
import NetWork from '@yl-app-jssdk/network';
import Vue from 'vue';

function callback(msg) {
  if (msg === 'logout') {
    // if (context.getIsLogged() && !window.location.hash.startsWith('#/login')) {
    return LoginService.logout();
    // }
  }
}

export default {
  openPage(url, params = {}) {
    return NetWork.getHasNetwork().then(hasNetwork => {
      if (hasNetwork) {
        const deviceId = (window.device && window.device.uuid) || '';
        const ticket = context.getUserContext('ticket') || '';
        let token = '';
        let tenantCode = '';
        if (context.getIsLogged()) {
          token = context.getToken();
          tenantCode =
            context.getUserContext('tenantCode') ||
            window.TENANT_CONFIG.TENANT_CODE;
        }

        params = Object.assign(
          {},
          {
            __channel__: 'cordova',
            deviceId,
            token,
            ticket,
            tenantCode,
            appid: window.TENANT_CONFIG.APP_ID,
            appkey: window.TENANT_CONFIG.APP_KEY,
            v: +new Date(),
          },
          params
        );
        // params = qs.stringify(params);
        // console.log('params ======> ', params);

        // 没有带域名，补全
        if (!~url.indexOf(window.GLOBAL_ENV.SSO_CENTER_URL)) {
          url = window.GLOBAL_ENV.SSO_CENTER_URL + url;
        }

        url += (~url.indexOf('?') ? '&' : '?') + qs.stringify(params);

        Plugin.openUrl(url, '', { navBar: null }, null, callback);
      } else {
        Vue.prototype.$toast('当前网络不可用');
      }
    });
  },
};
