// 开发环境，合并本地配置
if (process.env.NODE_ENV === 'DEV' || process.env.NODE_ENV === 'development') {
  let modulesLocalList = {};
  try {
    modulesLocalList = require('./modules.local.js');
  } catch (err) {
    console.error(
      '加载插件模块配置错误，请在根目录config下创建modules.local.js',
      err
    );
  }
  Object.keys(moduleList).map(item => {
    // 如果本地配置有设置，那么替换覆盖原来属性
    if (modulesLocalList[item]) {
      moduleList[item] = {
        ...moduleList[item],
        ...modulesLocalList[item],
      };
    }
  });
}

module.exports = moduleList;
