import ajax from '@src/common/ajax';
import { createHexSign } from '@src/utils/sign';
import context from '@yl-app-jssdk/context';
import guid from '@yl/guid';

const domain = window.GLOBAL_ENV.SSO_API;

const SMS_TYPE = {
  login: 'LOGIN_SMS',
  register: 'REGISTER_SMS',
  changePwd: 'CHANGE_PWD_SMS',
  bind: 'BIND_MOBILE_SMS',
};

function createConfig(data, sign = true) {
  data = Object.assign({}, data, { appid: window.TENANT_CONFIG.APP_ID });

  if (sign) {
    const signData = Object.assign({}, data, {
      appKey: window.TENANT_CONFIG.APP_KEY,
    });
    data.signature = createHexSign(signData);
  }

  return data;
}

export const login = data => {
  return ajax({
    method: 'post',
    url: `${domain}/v2/user/login`,
    data,
    retry: false,
    noReLogin: true,
  })
    .then(res => {
      return res.data;
    })
    .catch(err => {
      return Promise.reject(err);
    });
};

export const sendCode = function(phone, from = 'login') {
  let data = {
    type: SMS_TYPE[from],
    to: phone,
  };

  data = createConfig(data);

  return ajax({
    url: `${domain}/v1/sms/send-code`,
    method: 'POST',
    data,
    retry: false,
  }).then(res => {
    if (res.errcode) {
      return Promise.reject(res.message);
    } else {
      return Promise.resolve(true);
    }
  });
};

