/**
 * 获取租户列表
 */
export const relateTenantList = function() {
  // 接口支持token和ticket取数，ticket的时效是2小时，优先使用token，无token时再使用ticket
  const token = context.getToken();
  let data = { token };

  if (!token) {
    const ticket = context.getUserContext('ticket');
    data = { ticket };
  }

  return ajax({
    url: `${domain}/v2/user/relate-tenant-list`,
    method: 'get',
    data: data,
  }).then(res => {
    if (res.errcode) {
      return Promise.reject(res.message);
    } else {
      return Promise.resolve(res.data);
    }
  });
};

// 单点登录
export const singleSignOn = data => {
  const appid = window.TENANT_CONFIG.APP_ID;
  const appKey = window.TENANT_CONFIG.APP_KEY;
  const nonce = guid();
  const timestamp = parseInt(new Date().getTime() / 1000);
  const params = {
    appid,
    clientIdentity: {
      appid,
      timestamp,
      nonce,
      signature: createHexSign({ appid, appKey, nonce, timestamp }),
    },
    ssoType: 'THIRD_TOKEN',
  };
  const paramsData = Object.assign({}, params, data);
  return ajax({
    url: `${domain}/v2/sso/login`,
    method: 'post',
    data: paramsData,
    retry: false,
  }).then(res => {
    if (res.errcode) {
      return Promise.reject(res.message);
    } else {
      return Promise.resolve(res.data);
    }
  });
};
