import context from '@yl-app-jssdk/context';
import Sha1 from '@utils/encryption';
import Selector from '@src/components/popup-selector';
import { openMWebview } from '@yl-app-jssdk/webview';
import NetWork from '@yl-app-jssdk/network';
import Vue from 'vue';

/**
 * 目前token不缓存，每次重新生成
 * $token = sha1($app_code . $tenant_id . urlencode($user_name) . $phone . $key);
 */
const getToken = function(appCode, key, name, phone, tenantId) {
  // $token = sha1($app_code . $tenantId . urlencode($user_name) . $phone . $key);
  let token = appCode + tenantId + encodeURIComponent(name) + phone + key;
  let hexSha1 = Sha1.hex_sha1(token);

  return hexSha1;
};

const getFdcYunUrl = function(urlBase, appCode, key) {
  let user = context.getUserContext();
  let userName = user.account;
  let userPhone = user.mobile;
  let userId = user.id;
  let tenantId =
    window.GLOBAL_ENV.APP_ENV != 'prod' ? 'YDZJCSZH' : user.tenantCode;

  var token = getToken(appCode, key, userName, userPhone, tenantId);

  let params =
    'app_code=' +
    appCode +
    '&tenant_id=' +
    tenantId +
    '&phone=' +
    userPhone +
    '&user_name=' +
    encodeURIComponent(userName) +
    '&userguid=' +
    userId +
    '&token=' +
    token;

  return urlBase + params;
};

const openUrl = function(url) {
  openMWebview(url, {
    navBar: {
      barColor: '#FFFFFF',
      barButtonColor: '#000000',
      barTitle: '在线客服',
      barPosition: 1,
    },
    isShowRefresh: false,
    onOpen: () => {
      Selector.hide();
    },
    onError: err => {
      console.log('err ======> ', err);
      // Vue.prototype.$toast('无法连接在线客服，请稍后重试');
    },
  });
};

export default {
  open() {
    return NetWork.getHasNetwork().then(hasNetwork => {
      if (!hasNetwork) {
        Vue.prototype.$toast('您的当前网络不可用，请检查您的网络设置', 2000);
        return Promise.reject({
          message: '您的当前网络不可用，请检查您的网络设置',
        });
      }

      let config = window.GLOBAL_ENV.ONLINE_HEPLER;
      let list = [];

      config.list.forEach(v => {
        let url = getFdcYunUrl(config.host, v.appCode, v.key);
        list.push({
          name: v.name,
          appCode: v.product,
          url,
        });
      });

      if (list.length === 1) {
        return openUrl(list[0].url);
      }

      console.log('openMWebview ====> ', openMWebview);

      return Selector.open({
        list,
        onClick: function(item) {
          openUrl(item.url);
          console.log(item);
        },
      });
    });
  },
};
