import { getUserPermissions as getUserPermissionsApi } from '@api/permission';
import _ from 'underscore';
import localstorageApi from '@yl/localstorage';
import Network from '@yl-app-jssdk/network';
import CONST from '@src/config/const';

const KEY = CONST.USER_PERMISSIONS;

const getPermissionsCache = (code = '') => {
  const data = localstorageApi.getObject(KEY, localstorageApi.level.user);
  console.log('getPermissionsCache ======> ', data);
  if (code) {
    return data[code] || [];
  } else {
    return data;
  }
};

export const getUserPermissions = code => {
  console.log('getUserPermissions======> ', code);

  return Network.getHasNetwork().then(hasNetwork => {
    if (hasNetwork) {
      return getUserPermissionsApi(code)
        .then(data => {
          if (!code) {
            return data;
          }
          let permissions = [];
          console.log('data ==> ', data);
          _.each(data[code], (funcs, menu) => {
            if (funcs.length) {
              permissions.push(`${menu}`);
              funcs.forEach(func => {
                if (menu === func) {
                  permissions.push(func);
                } else {
                  permissions.push(`${menu}_${func}`);
                }
              });
            }
          });

          let cache =
            localstorageApi.getObject(KEY, localstorageApi.level.user) || {};

          cache[code] = permissions;

          localstorageApi.set(KEY, cache, localstorageApi.level.user);
          return Promise.resolve(permissions);
        })
        .catch(err => {
          console.log('check quality getUserPermissions error', err);
          return Promise.resolve(getPermissionsCache(code));
        });
    } else {
      return Promise.resolve(getPermissionsCache(code));
    }
  });
};
